import React from 'react';
import { useState, useEffect} from "react";
import { useNavigate } from 'react-router-dom';
//
import {Button,Accordion,Container,Row} from 'react-bootstrap';
//
import Swal from 'sweetalert2' 
//API
import RecommendService from '../../services/recommend.service';
//    
import Error from '../../page/recommend/result1/Error';

// 
import "../main/PMainHeader.css";
import "../main/PMainFrame.css";
import "../login/Plogin.css";

function PResult1() {
    //     
    const [recommandService, setRecommandService] = useState(true); //    
    const [predictedRate, setPredictedRate] = useState(false);
    const [majorInfo, setMajorInfo] = useState(false);
    const [serviceIntro, setServiceIntro] = useState(false);

    /**1    */
    // 
    const [thisResult, setThisResult] = useState(false); //  
    const [isError, setIsError] = useState(false); //    
    const [answer, setAnswer] = useState(false); //   


    //   
    let navigate = useNavigate();
    
    /**    */
    //    
    const handleSelectService = (type, state) => {
        
        //     
        if(!type){
            resetSelectedTab();
        }
        else if(type === "r"){
            //     
            selectRecommandService(state);
            showPageMovePopUp("  ","/recommend");
        }
        else if(type === "p"){
            //     
            selectsetPredictedRate(state);
            showPageMovePopUp(" ");
        }
        else if(type === "m"){
            //     
            selectMajorInfo(state);
            showPageMovePopUp("  ");
        }
        else if(type === "i"){
            //     
            selectServiceIntro(state);
            showPageMovePopUp(" ");
        }
    }

    //    css  
    const resetSelectedTab = () =>{
        setRecommandService(false);
        setPredictedRate(false);
        setMajorInfo(false);
        setServiceIntro(false);        
    }

    const selectRecommandService = (state) =>{
        let reverseState = false;

        setRecommandService(state);
        setPredictedRate(reverseState);
        setMajorInfo(reverseState);
        setServiceIntro(reverseState);
    }

    const selectsetPredictedRate = (state) =>{
        let reverseState = false;

        setRecommandService(reverseState);
        setPredictedRate(state);
        setMajorInfo(reverseState);
        setServiceIntro(reverseState);
    }

    const selectMajorInfo = (state) =>{
        let reverseState = false;

        setRecommandService(reverseState);
        setPredictedRate(reverseState);
        setMajorInfo(state);
        setServiceIntro(reverseState);
    }

    const selectServiceIntro = (state) =>{
        let reverseState = false;

        setRecommandService(reverseState);
        setPredictedRate(reverseState);
        setMajorInfo(reverseState);
        setServiceIntro(state);
    }

    /**     */
    const showPageMovePopUp = (type, url="/") =>{
        Swal.fire({
          text: `"${type}"() ?`,
          icon: undefined,
          confirmButtonText: '',
          confirmButtonColor: '#145f7a',
          showCancelButton: true,
          cancelButtonText: ''
        }).then((result) => {
          /* Read more about isConfirmed, isDenied below */
          if (result.isConfirmed) {
            navigate(url)
          } else if (result.isDenied) {
            return
          }
        });
      }

    /**1  */
    useEffect(() => {
        //  
        let testKeyValidate = sessionStorage.getItem('testKey');
        let resultType = sessionStorage.getItem('result1Type');

        //      
        RecommendService.getFirstSectionResult(resultType, testKeyValidate).then(
            (response) => {

                // console.log("getData:", response.data)

                //    
                sessionStorage.setItem('testKey', response.data.testKey);

                // console.log("response.data.list",response.data.list);

                //   
                setThisResult(response.data.list);
                //
                //ShowResult();
            }
        ).catch(
            (Error) => {
                //  
                setIsError(true);
                
            }
        )

        //()
        //setThisResult(testData.list);
        //thisResult    
        
        ShowResult();
    },[])

    /**1   */
    
    const ShowResult = () => {
        // console.log('thisResult:',thisResult);
        // console.log('testData.list.academicName:',thisResult[0]);
        // console.log('testData.list.academicName:',testData.list[0]);

        if(!thisResult){
            return;
        }

        return(
            <>
                <div className="question-container">
                    <span className='notice'>    1 !<br/>      .</span>
                    </div>
                <div className="response-container">
                    <Accordion style={{width: "90%"}}>
                        {
                            //testData.list.map(thisData => (
                            thisResult.map(thisData => (
                                <>
                                    <Accordion.Item eventKey={thisData.academicName}>
                                        <div id={`${thisData.academicName}`} onClick={selectResult}>
                                            <Accordion.Header>{thisData.academicName}</Accordion.Header>
                                        </div>
                                        <Accordion.Body>
                                            {thisData.departmentList}
                                            {/* <ListGroup>
                                                <ListGroup.Item>{thisData.departmentList}</ListGroup.Item>
                                            </ListGroup> */}
                                        {/* <div className='eachResult' key={thisData.academicName} data-tip data-for={`tooltip${thisData.academicName}`} onClick={()=>{selectAcademicName(thisData.academicName)}}>
                                            
                                                {thisData.academicName}
                                            
                                                <ReactTooltip
                                                    id={`tooltip${thisData.academicName}`}
                                                    effect="solid"
                                                    place="bottom"
                                                    type="dark"
                                                    key={thisData.departmentList}
                                                    >
                                                    {thisData.departmentList}
                                                </ReactTooltip>
                                        </div> */}
                                        </Accordion.Body>
                                    </Accordion.Item>
                                </>
                            ))
                        }
                    </Accordion>
                    <Button className='confirm-btn' onClick={() => goToNext()}></Button>
                </div>
            </>
        )
    }

    /**1   */
    const selectResult =(event) =>{
        let result = event.currentTarget.id;

        // console.log("thisAcademicName:",result);
        setAnswer(result);


        //    ( )
        document.getElementById(result).style.border = "1px solid #002F5A";
        document.getElementById(result).style.color = "white";
    }

    /**2   */
    const goToNext = () => {
        //      
        if(answer !== false){
            //  
            let testKeyValidate = sessionStorage.getItem('testKey');

            //API
            RecommendService.submitFirstSectionResult(answer, testKeyValidate).then(
                (response) => {
                    sessionStorage.setItem('recommendFirstResult', true);
                    sessionStorage.setItem('questionNum', 1);
        
                    //2  page 
                    navigate("/question2");
                    window.location.reload();
                }
            );
            // console.log("answer:",answer);

        }
        else{
            Swal.fire({
                text: " ",
                icon: undefined,
                confirmButtonText: '',
                confirmButtonColor: '#002F5A'
              });
        }
    }

    return (
        <div>
            {/* Header */}
            <div className='main-header'>
                <div className='main-icon' onClick={()=>navigate('/')}>
                    <img id='hufs-icon-white'src={require('../../media/main/().gif')} alt=" "/>
                    <span id='main-name'> ?</span>
                </div>
                <div className='main-select-service-wrap'>
                    {
                        !recommandService?
                        <div className='main-select-service-tab'>
                            <span onClick={()=>handleSelectService('r', true)}></span>
                        </div>:
                        <div className='selected-main-select-service'>
                            <span onClick={()=>handleSelectService('r', false)}></span>
                        </div>
                    }

                    {
                        !predictedRate?
                        <div className='main-select-service-tab'>
                            <span onClick={()=>handleSelectService('p', true)}></span>
                        </div>:
                        <div className='selected-main-select-service'>
                            <span onClick={()=>handleSelectService('p', false)}></span>
                        </div>
                    }

                    {
                        !majorInfo?
                        <div className='main-select-service-tab'>
                            <span onClick={()=>handleSelectService('m', true)}></span>
                        </div>:
                        <div className='selected-main-select-service'>
                            <span onClick={()=>handleSelectService('m', false)}></span>
                        </div>
                    }

                    {
                        !serviceIntro?
                        <div className='main-select-service-tab'>
                            <span onClick={()=>handleSelectService('i', true)}> </span>
                        </div>:
                        <div className='selected-main-select-service'>
                            <span onClick={()=>handleSelectService('i', false)}> </span>
                        </div>
                    }
                </div>
                <div className='login-wrap'>
                    {/*      */}
                </div>
            </div>
            {/* //Header */}

            {/* 1     */}
                <div className="start-question-layer"/><br/>
                    <div className="question-wrap">
                        {
                            !isError?
                            <>
                                {
                                    !thisResult?
                                    <></>:
                                    <ShowResult/>  
                                }
                            </>:
                                <Error/>
                        }
                    </div>
            {/* //1     */}
        </div>
    );
}

export default PResult1;