import React, {useEffect, useState, useRef} from 'react';
// import { useForm, Controller} from 'react-hook-form';
// import styled from 'styled-components'
// import axios from 'axios'
import ShowContract from './component/ShowContract';
import Header from '../../main/component/Header';
import '../../../media/css/formFrame.css';
// import OnlyPrevFooter from '../../../common/footer/OnlyPrevFooter';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, Col,  Container, Modal, Row, OverlayTrigger, Tooltip} from 'react-bootstrap';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import AuthService from '../../../services/auth.service';
// import { isEmail } from "validator";
import { useNavigate} from 'react-router-dom';
import Select from 'react-bootstrap/FormSelect'//bootstrap   Select (  Form.select    )
import '../../../media/css/commonFrame.css'
// import Footer from '../../main/component/Footer';
import Swal from 'sweetalert2'

//input    
const required = (value) => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
         !
      </div>
    );
  }
};

//stdNum()
const vuserstdNum = (value) => {
  if (value.length < 4 || value.length > 9) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
         / .
      </div>
    );
  }
};

//nickName
const vusername = (value) => {
  if (value.length < 2 || value.length > 10) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
         4~10 .
      </div>
    );
  }
};

//password
const vpassword = (value) => {
  if (value.length < 6 || value.length > 20) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
         6~20 .
      </div>
    );
  }
};

//gpa
const vgpa = (value) => {
  if (value <= 0 || value > 4.5) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
          .
      </div>
    );
  }
};

export default function SignupForm() {
  //  

  const [totalFirstMajor, setTotalFirstMajor] = useState(''); //     
  const [totalDualMajor, setTotalDualMajor] = useState(''); //     
  const [userType, setUserType] = useState('mentee'); //   
  const [dualmajor, setDualmajor] = useState('');//      
  const [show, setShow] = useState(false);//  
  const [confirm, setConfirm] = useState(true); //  

  const form = useRef();
  const checkBtn = useRef();
  const [username, setUsername] = useState("");
  const [userstdNum, setUserstdNum] = useState("");
  const [password, setPassword] = useState("");
  const [grade, setGrade] = useState("1");
  const [firstMajor,  setFirstMajor] = useState(false);
  const [dualMajor, setDualMajor] = useState(false);
  const [gpa, setGpa] = useState(false);
  const [successful, setSuccessful] = useState(false);
  const [message, setMessage] = useState("");

  const [checkStdNum, setCheckStdNum] = useState('');

  //  
  const showMenu = false;

  //  : 
  const showPrev = true;
  const showNext = false;
  const showDev = false;

  //
  const [majorAlertModal, setMajorAlertModal] = useState(false);

  let navigate = useNavigate();

  //   
  const onChangeUsername = (e) => {
    const username = e.target.value;
    setUsername(username);
  };

  const onChangeUserstdNum = (e) => {
    const userstdNum = e.target.value;
    setUserstdNum(userstdNum);
    
  };

  const onChangePassword = (e) => {
    const password = e.target.value;
    setPassword(password);
  };

  //   
  //select    
  const onChangeUserGrade = (e) => {
    const userGrade = e.target.value;
    setGrade(userGrade);
  }

  const onChangeUserGpa = (e) => {
    const userGpa = e.target.value;
    setGpa(userGpa);
  }

  const SelectedUserType= (selected) => {
    //   
    if(selected.target.value === "mento"){
      setUserType("mento");
      setDualmajor('()');
    }

    //   
    else{
      setUserType("mentee");
      setDualmajor('');
    }
  }

  const onChangeUserFirstMajor = (e) =>{
    const userFirstMajor = e.target.value;

    //     
    if(userFirstMajor !== dualMajor){
      setFirstMajor(userFirstMajor);
      return;
    }
    setFirstMajor(false);

    Swal.fire({
      text: "    ",
      icon: undefined,
      confirmButtonText: '',
      confirmButtonColor: '#002F5A'
    });
  }

  const onChangeUserDualMajor = (e) =>{
    const userDualMajor = e.target.value;

    //     
    if(userDualMajor !== firstMajor){
      setDualMajor(userDualMajor);
      return;
    }
    setDualMajor(false);

    Swal.fire({
      text: "    ",
      icon: undefined,
      confirmButtonText: '',
      confirmButtonColor: '#002F5A'
    });
  }

  //stdNum 
  const stdNumCheckDuplicate = () => {
    //stdNum   
    if(userstdNum === ''){
      required(userstdNum);
    }
    //stdNum    
    else if(userstdNum.length < 4 || userstdNum.length > 9){
      vuserstdNum(userstdNum);
    }
    //stdNum   
    else{
      AuthService.checkDuplicate(userstdNum).then(
        (response) => {
          // stdNum  
          // console.log(response.data.joinPossible)
          
          if(response.data.joinPossible === true){
            setCheckStdNum(true);

            Swal.fire({
              text: " /.",
              icon: undefined,
              confirmButtonText: '',
              confirmButtonColor: '#002F5A'
            });
          }
          else
            Swal.fire({
              text: "  /.",
              icon: undefined,
              confirmButtonText: '',
              confirmButtonColor: '#002F5A'
            });
        },
        (error) => {
          Swal.fire({
            text: " .",
            icon: undefined,
            confirmButtonText: '',
            confirmButtonColor: '#002F5A'
          });
        }
      );
    }
  }

  //    API  
  const handleRegister = (e) => {
    e.preventDefault();
    setMessage("");
    setSuccessful(false);
    form.current.validateAll();
    
    ///   
    if (checkStdNum === false){
      Swal.fire({
        text: "/  .",
        icon: undefined,
        confirmButtonText: '',
        confirmButtonColor: '#002F5A'
      });
      return;
    }

    //  
    if(firstMajor === false){
      Swal.fire({
        text: " .",
        icon: undefined,
        confirmButtonText: '',
        confirmButtonColor: '#002F5A'
      });
      return;
    }

    //  
    if(dualMajor === false){
      Swal.fire({
        text: " .",
        icon: undefined,
        confirmButtonText: '',
        confirmButtonColor: '#002F5A'
      });
      return;
    }

    if(firstMajor === dualMajor){
      Swal.fire({
        text: "    ",
        icon: undefined,
        confirmButtonText: '',
        confirmButtonColor: '#002F5A'
      });
      return;
    }

    if (checkBtn.current.context._errors.length === 0) {
      AuthService.register(userstdNum, password, username, grade, userType, firstMajor, dualMajor, gpa).then(
        (response) => {
          setMessage(response.data.message);
          setSuccessful(true);
          
          let newUser = {"stdNum":userstdNum, "nickName": username, "grade": grade, "userType": userType, "firstMajor": firstMajor, "dualMajor": dualMajor, "gpa": gpa};
          // 
          sessionStorage.setItem("user", JSON.stringify(newUser));

          //   
          Swal.fire({
            text: " ?  ",
            icon: undefined,
            confirmButtonText: '',
            confirmButtonColor: '#002F5A'
          });

          //main page 
          navigate("/");
          window.location.reload();
        },
        (error) => {
          const resMessage =
            "  ."
            // (error.response &&
            //   error.response.data &&
            //   error.response.data.message) ||
            // error.message ||
            // error.toString();
          setMessage(resMessage);
          setSuccessful(false);
        }
      );
    }
  };

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  //   
  useEffect(  () =>{
    //  /    
    AuthService.firstMajorList();
    AuthService.dualMajorList();

    let allFirstMajor = false;
    let allDualMajor = false;
    if(localStorage.getItem('firstMajor') !== null){
        allFirstMajor = Object.values(JSON.parse(localStorage.getItem('firstMajor')));
    }
    if(localStorage.getItem('dualMajor') !== null){
        allDualMajor = Object.values(JSON.parse(localStorage.getItem('dualMajor')));
    }

    
    //   
    setTotalFirstMajor(allFirstMajor);
    //   
    setTotalDualMajor(allDualMajor);   
    
    
    //  
    setFirstMajor(allFirstMajor[0].id);
    setFirstMajor(allDualMajor[0].id);

    //       
    // setTotalFirstMajor(exampleFirstMajor);
    // setTotalDualMajor(exampleDualMajor);
  },[])


  //       
  // const exampleFirstMajor = [{id: '1', name: "GBT"}, {id: '2', name:""}, {id: '3', name:" "} ]
  // const exampleDualMajor = [{id: '0', name: ""}, {id: '1', name: "GBT"}, {id: '2', name:""}, {id: '3', name:" "} ]
  


  return (
      <div className="mainContainer">
        <div className="header"><Header showMenu={showMenu}/></div>
        <div className='mainBody'>
        <div className='formFrame'>
          <Form onSubmit={handleRegister} ref={form}>
            {!successful && (
              <Container>
                <Row className='tableRow'>
                  <Col  md={12} xs={12}>
                  <span className='comment'>,<br/>
                         !</span>
                  </Col>
                </Row>
                
                <hr/>
                <Row  className='tableRow'>
                  <Col md={5} xs={5}>
                  <OverlayTrigger
                  key='stdNumInfo'
                  placement='top'
                  overlay={
                    <Tooltip id="stdNumInfo">
                        
                    </Tooltip>
                  }
                  >
                    <span className='titleStyle'>/</span>
                  </OverlayTrigger>
                  </Col>
                  <Col md={7} xs={7}>
                    <Input
                      type="userstdNum"
                      className="form-control"
                      name="userstdNum"
                      value={userstdNum}
                      onChange={onChangeUserstdNum}
                      validations={[required, vuserstdNum]}
                    />
                  </Col>
                  
                </Row>
                <Row  className='tableRow'>
                <Col md={5} xs={5}>
                    <Button type='button' className='buttonDuplicate' onClick={stdNumCheckDuplicate} ></Button>
                  </Col>
                <Col md={7} xs={7}></Col>
                </Row>
                <br/>

                <Row className='tableRow'>
                  <Col md={5} xs={5}>
                    <span className='titleStyle'></span>
                  </Col>
                  <Col md={7} xs={7}>
                    <Input
                        type="username"
                        className="form-control"
                        name="username"
                        value={username}
                        onChange={onChangeUsername}
                        validations={[required, vusername]}
                      />
                  </Col>
                </Row>
                <Row  className='tableRow'>
                  <Col md={5} xs={5}>
                    <span className='titleStyle'></span>
                  </Col>
                  <Col md={7} xs={7}>
                    <Input
                      type="password"
                      className="form-control"
                      name="password"
                      value={password}
                      onChange={onChangePassword}
                      validations={[required, vpassword]}
                    />
                  </Col>
                </Row>

                <br/>
                <Row  className='tableRow'>
                  <Col md={5} xs={12}>
                    <span className='titleStyle'></span>
                  </Col>
                  <Col md={7} xs={12}>
                    <Select className='inputStyle' onChange={onChangeUserFirstMajor}>
                    {
                      !totalFirstMajor?  
                      <option value="0"> </option>:
                      totalFirstMajor.map(thisMajor => (
                        <option key={thisMajor.id} value={thisMajor.id}>
                          {thisMajor.name}
                        </option>
                      ))
                    }
                    </Select>
                  </Col>
                </Row>

                <Row  className='tableRow'>
                  <Col md={5} xs={12}>
                    <span className='titleStyle'></span>
                  </Col>
                  <Col md={7} xs={12}>
                    <Select className='inputStyle' onChange={onChangeUserGrade}>
                      <option value="1">1</option>
                      <option value="2">2</option>
                      <option value="3">3</option>
                      <option value="4 ">4 </option>
                    </Select>
                  </Col>
                </Row>
                <Row  className='tableRow'>
                    <Col md={5} xs={12}>
                    <OverlayTrigger
                      key='stdNumInfo'
                      placement='top'
                      overlay={
                        <Tooltip id="stdNumInfo">
                          <br/>
                          : ()  .<br/>
                          :  () .
                        </Tooltip>
                      }
                      >
                      <span className='titleStyle'></span>
                    </OverlayTrigger>
                    </Col>
                    <Col md={7} xs={12}>
                      <Select className='inputStyle' onChange={SelectedUserType}>
                        <option value="mentee"></option>
                        <option value="mento"></option>
                      </Select>
                    </Col> 
                </Row>

                <Row  className='tableRow'>
                  <Col md={5} xs={12}>
                    <span className='titleStyle'>{dualmajor}</span>
                  </Col>
                  <Col md={7} xs={12}>
                    <Select className='inputStyle' onChange={onChangeUserDualMajor}>
                    {
                      !totalDualMajor?  
                      <option value="0"> </option>:
                      totalDualMajor.map(thisMajor => (
                        <option key={thisMajor.id} value={thisMajor.id}>
                          {thisMajor.name}
                        </option>
                      ))
                    }
                    </Select> 
                  </Col>
                </Row>

                <Row className='tableRow'>
                  <Col md={5} xs={5}>
                    <span className='titleStyle'> </span>
                  </Col>
                  <Col md={7} xs={7}>
                    <Input
                        type="number" 
                        step="0.01"
                        className="form-control"
                        name="gpa"
                        value={gpa}
                        onChange={onChangeUserGpa}
                        validations={[required, vgpa]}
                      />
                  </Col>
                </Row>
              </Container>
            )}

            <br/>
            <Container>
              <Row className='tableRow'>
                <Col md={8} xs={6}></Col>
                <Col md={4} xs={6}>
                  <span className='contract'></span>
                </Col>
                <Col md={8} xs={6}></Col>
                <Col md={4} xs={6}>
                  <Button type='button' className='buttonContract' onClick={handleShow}>
                    
                  </Button>
                </Col>
              </Row>

              <br/>
              <Row  className='tableRow'>
                <Col md={12} xs={12}>
                  <span className='registerNotice'>   .</span>
                </Col>
                <Col md={12} xs={12}>
                  <Button className='buttonRegister' type="submit" disabled={confirm}></Button>
                </Col>
              </Row>

              {message && (
                <div className="form-group">
                  <div
                    className={ successful ? "alert alert-success" : "alert alert-danger" }
                    role="alert"
                  >
                    {message}
                  </div>
                </div>
              )}
              <CheckButton style={{ display: "none" }} ref={checkBtn} />

            </Container>
          </Form>

          <Modal show={show} fullscreen={true} onHide={handleClose}>
            <Modal.Header closeButton>
              <Modal.Title></Modal.Title>
            </Modal.Header>
            <Modal.Body><ShowContract/></Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={ () => {
                handleClose();
                setConfirm(true);
              }}>
                
              </Button>
              <Button style={{backgroundColor: "#002F5A", opacity: "0.8"}} onClick={() => {
                handleClose();
                setConfirm(false);
              }}>
                
              </Button>
            </Modal.Footer>
          </Modal>
          
          </div>
          </div>
        {/* <div className='footer'><Footer showPrev={showPrev} showNext={showNext} showDev={showDev}/></div> */}

        {/* <MyAlert title={""} alertContent={"    "}  show={majorAlertModal} onHide={() => setMajorAlertModal(false)}/> */}
      </div>
  )
}


