import React, { useState, useEffect} from 'react'
import { Button, Col, Container, Modal, Row } from 'react-bootstrap'
import styled from 'styled-components'
// import axios from 'axios'
import AuthService from '../../../services/auth.service'
import { useNavigate, Link } from 'react-router-dom';
import Swal from 'sweetalert2'   

export default function MyModal(props) {
  // 

  // const [thisUser, setThisUser] = useState('')// 
  
  //  
  const [userName, setUserName] = useState('')//   
  const [userType, setUserType] = useState('mentee')// (/ )
  const [userstdNum, setUserstdNum] = useState('202200001')// ( )
  const [userMajor, setUserMajor] = useState(''); // 
  const [userGrade, setUserGrade] = useState('1'); // 
  const [dualMajor, setDualMajor] = useState(''); //
  const [dualMajorType, setDualMajorType] = useState(' '); //,     

  //backend  userdata API
  let thisUser = false;
  if(sessionStorage.getItem('user') !== null){
    thisUser = Object.values(JSON.parse(sessionStorage.getItem('user')));
  }


  // (call-back)
  let navigate = useNavigate();

  //      
  useEffect(() => {
    
    //
    // console.log("thisUser",thisUser);

    AuthService.firstMajorList();
    AuthService.dualMajorList();

    let firstMajorList = [JSON.parse(localStorage.getItem('firstMajor'))];
    let dualMajorList = [JSON.parse(localStorage.getItem('dualMajor'))];

    //    value   thisUser 
    if (thisUser !== false){
      //   (   !)
      setUserstdNum(thisUser[0]); ///
      setUserName(thisUser[1]); //
      setUserGrade(thisUser[2]);  //
      setUserType(thisUser[3]);   // 

      let firstMajorIndex = Number(thisUser[4]);
      let dualMajorIndex = Number(thisUser[5]);

      // console.log('firstMajorName :',firstMajorList );
      // console.log('dualMajorIndex :',dualMajorList );
      // console.log('firstMajorIndex :',firstMajorIndex);
      // console.log('dualMajorIndex :',dualMajorIndex);

      if(!firstMajorList && !dualMajorList){
        let firstMajorName = firstMajorList.find((e) => {return e.id == firstMajorIndex;});
        let dualMajorName = dualMajorList.find((e) => {return e.id == dualMajorIndex;});
        setUserMajor(firstMajorName.name);// 
        setDualMajor(dualMajorName.name);// 
      }


      //setUserMajor(thisUser[4]);  
      //setDualMajor(thisUser[6]);  
    }
  },[])


  //userType  dualMajorType 
  useEffect(()=>{
    if(userType === 'mento'){
      setDualMajorType('()');
    }
    else{
      setDualMajorType(' ');
    }
  },[userType]);


  //   
  const goToRecommendResult = () => {
    let departmentName = localStorage.getItem('recommendResult');

    //     
    if(!departmentName){
      Swal.fire({
        text: "   ",
        icon: undefined,
        confirmButtonText: '',
        confirmButtonColor: '#002F5A'
      });

      navigate('/recommend');
    }

    navigate('/recommendResult');
 
  }

  //   
  const goToSavedMajor = () => {
      navigate("/showMyMajorInfo");
  }

  //    
  const goToShowMyRateInfo = () => {
    navigate('/showMyRateInfo');
  }


  return (
    <Modal {...props} aria-labelledby="contained-modal-title-vcenter">
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter" >
          <Container>
            <Row>
              <Col md={12} xs={12} >
                <h6><b>{userName} {userType}, </b></h6>
              </Col>
              <Col md={12} xs={12} >
                <h6><b>.</b></h6>
              </Col>
            </Row>
          </Container>
        </Modal.Title>
      </Modal.Header>
      <Modal.Body className="show-grid">
        <Container>
          <Row>
            <Col xs={6} md={6}>
              <small><b>/:</b></small>
            </Col>
            <Col xs={6} md={6}>
            <small>{userstdNum}</small>
            </Col>

            {/* <Col xs={6} md={6}>
              <small><b>:</b></small>
            </Col>
            <Col xs={6} md={6}>
            <small>{userMajor}</small>
            </Col> */}

            <Col xs={6} md={6}>
              <small><b>:</b></small>
            </Col>
            <Col xs={6} md={6}>
              <small>{userGrade}</small>
            </Col>

            {/* <Col xs={6} md={6}>
              <small><b>{dualMajorType}:</b></small>
            </Col>
            <Col xs={6} md={6}>
              <small>{dualMajor}</small>
            </Col> */}

            <Col xs={12} md={12}>
              <Modify>
                <Link to={'/editInfo'}>
                  <Button className='modify'></Button>
                </Link>
              </Modify>
            </Col>

            <PersonalButton>
              <Col xs={12} md={12}>
                <Button  className='recommend' onClick={goToRecommendResult}>   </Button>
              </Col>

              <Col xs={12} md={12}>
                <Button className='compete' onClick={goToShowMyRateInfo}>   </Button>
              </Col>

              <Col xs={12} md={12}>
                <Button className='myPost'  onClick={goToSavedMajor}>  </Button>
              </Col>
            </PersonalButton>
          </Row>

        </Container>
      </Modal.Body>
      <Modal.Footer>
        <Cancel>
          <Button className="cancel" onClick={ ()=> {
            //
            AuthService.logout();

            //main page 
            navigate("/");
            window.location.reload();
          
          }}></Button>
        </Cancel>
      </Modal.Footer>
    </Modal>
  )
}

//  CSS
const Modify = styled.div`
  text-align: right;
  padding-right: 5%;

  .modify{
    /**/
    background-color: #5a5a5a;
    opacity: 1;

    font-size: 12px;
    margin-top: 10px;
    margin-bottom: 20px;
    color: white;

    width: 40%;
    height: 20%;

    /**/
    &:hover {
        opacity: 0.9;
    }
  }
`


//   CSS
const PersonalButton = styled.div`
  text-align: center;

  .recommend{
    /**/
    background-color: #002F5A;
    opacity: 0.8;

    font-size: 14px;
    width: 90%;
    height: 40%;
    margin-bottom: 8px;

    /**/
    &:hover {
        background-color: #002F5A;
        opacity: 0.9;
      }
  }

  .compete{
    /**/
    background-color: #028799;
    opacity: 0.9;

    font-size: 14px;
    width: 90%;
    height: 40%;
    margin-bottom: 8px;

    /**/
    &:hover {
        background-color: #028799;
        opacity: 1;
      }
  }

  .myPost{
    /**/
    background-color: #875100;
    opacity: 0.8;

    font-size: 14px;
    width: 90%;
    height: 40%;
    margin-bottom: 5px;

    /**/
    &:hover {
        background-color: #875100;
        opacity: 0.9;
      }
  }
`

const Cancel = styled.div`
  .cancel{
    /**/
    background-color: #5a5a5a;
    opacity: 1;

    font-size: 12px;
    margin-top: 10px;
    margin-bottom: 20px;
    color: white;

    /**/
    &:hover {
        opacity: 0.9;
    }
  }
`

