//  
import {useState, useEffect} from "react";
// import axios from "axios";
import styled from "styled-components";
import Header from "../main/component/Header";
import Footer from "../main/component/Footer";

import '../../media/css/commonFrame.css';
import { Button, Form, Modal, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { useNavigate } from "react-router-dom";
import RateService from '../../services/rate.service';

// import RecommendService from "../../services/recommend.service";
import Swal from 'sweetalert2';   
// import Login from "../../../components/Login";
import GPAChart from '../../page/rate/component/GPAChart';
import ApplyChart from "../../page/rate/component/ApplyChart";


function ShowMyRateInfo() {
    //  :   
    const showMenu = false;

    //  
    const showPrev = true;
    const showNext = false;
    const showDev = false;

    // 
    let navigate = useNavigate();

    //,   
    const selectCampus = (element) => {
        //  
        if(element.target.id === "global")
            navigate("/global");
    }

    //filter       API 

    // 
    const [thisMajorList, setThisMajorList] = useState([{id: "1", name: ""}]);
    const [selectedMajorId, setSelectedMajorId] = useState("");
    const [majorInfo, setMajorInfo] = useState("");

    //  ( :  false)
    const [login, setLogin] = useState(false);
    const [thisUser, setThisUser] = useState('');

    //  ( : API )
    const [applyInfo, setApplyInfo] = useState(false); //stdNum: , apply: boolean, majorName: DB , gpa: , change: boolean
    const [thisApply, setThisApply] = useState(false);
    const [clicked, setClicked] = useState(false);
    const [valid, setValid] = useState(true);

    //  
    const [showModal, setShowModal] = useState(false);
    const [thisGpa, setThisGpa] = useState("");

    //API 
    //        
    //    
    useEffect(() => {

        // //
        // console.log("rendering")
        // let data = `
        //     [
        //         {
        //             "id": "1",
        //             "name": "GBT"
        //         },
        //         {
        //             "id": "2",
        //             "name": ""
        //         },
        //         {
        //             "id": "3",
        //             "name": ""
        //         },
        //         {
        //             "id": "4",
        //             "name": ""
        //         }
        //     ]
        // `
        // setThisMajorList(Object.values(JSON.parse(data)));

        RateService.getMajorListSeoul().then(
            (response) => {
                let getData = response.data.majorListSeoul;
                setThisMajorList(getData);
                setSelectedMajorId(getData[0].name);
                // console.log(response.data.majorListSeoul);
            }
        )

        //   
        //   
        if(sessionStorage.getItem("user")!==null && sessionStorage.getItem("user")!==undefined){
            let tmp = Object.values(JSON.parse(sessionStorage.getItem("user")));
            // console.log("sessionLog:", tmp);
            let userId = tmp[0];
            setThisUser(userId);
            setLogin(true); 

            // console.log("user id:", userId);

            //06.01 
            //    
            RateService.getApplyInfo(userId).then(
                (response) =>{
                    //API   stdNum: , apply: boolean, majorName: DB , gpa: , change: boolean
                    setApplyInfo(response.data);
                    setThisApply(response.data.apply);
                    setValid(response.data.change);
                    // console.log("applyInfo data:", response.data);

                    if(response.data.apply == true){
                        //     
                        setSelectedMajorId(response.data.majorName);
                    }
                    
                }
            )
        }
        //  
        else{
            setLogin(false);
        }

    },[])

    // useEffect(() => {
    //     //major  or major  
    //     if(login){ //&& (!selectedMajorId == false)){
    //         //    
    //         RateService.getApplyInfo(thisUser).then(
    //             (response) =>{
    //                 //API   stdNum: , apply: boolean, majorName: DB , gpa: , change: boolean
    //                 setApplyInfo(response.data);
    //                 setThisApply(response.data.apply);
    //                 console.log("applyInfo data:", response.data);
    //             }
    //         )
    //     }

    //     // o and     
    //     if(login && applyInfo[2]!== null){
    //         setSelectedMajorId(applyInfo[2]);
    //     }
    //     //   x 
    //     else{
    //         setSelectedMajorId(thisMajorList[0].name);
    //     }

    // },[thisMajorList])

    //select    API 
    useEffect(() => {
        // //
        // let majorData =`
        //     {
        //         "id" : "1",
        //         "name" : "GBT",
        //         "applyNum" : "25",
        //         "totalNum" : "100",
        //         "avgGpa" : "4.05"
        //     }
        // `
        // setMajorInfo(JSON.parse(majorData));

        if(selectedMajorId){
            RateService.getRateInfo(selectedMajorId).then(
                (response) => {
                    setMajorInfo(response.data);
    
                    // console.log("getRateInfo:", response.data);
                }
            )
        }

        //   
        //   
        if(sessionStorage.getItem("user")!==null && sessionStorage.getItem("user")!==undefined){
            let tmp = Object.values(JSON.parse(sessionStorage.getItem("user")));
            //   console.log("sessionLog:", tmp);
              let userId = tmp[0];
            setThisUser(userId);
            setLogin(true); 

            // console.log("user id:", userId);
            //06.01 
            //    
            RateService.getApplyInfo(userId).then(
                (response) =>{
                    //API   stdNum: , apply: boolean, majorName: DB , gpa: , change: boolean
                    setApplyInfo(response.data);
                    setThisApply(response.data.apply);
                    setValid(response.data.change);
                    // console.log("applyInfo data:", response.data);

                    if(response.data.apply == true){
                        //     
                        setSelectedMajorId(response.data.majorName);
                    }
                    
                }
            )
        }
        //  
        else{
            setLogin(false);
        }

    },[selectedMajorId])

    //    
    useEffect(() => {
        // & thisApply === true 
        if(login && (thisApply == true) && (clicked === true)){
            RateService.postApply(thisUser, selectedMajorId, thisApply).then(
                (response) =>{
                    // console.log("post selectedMajorId:", selectedMajorId);
                    // console.log("user id:", thisUser);
                    // window.location.reload();
                    Swal.fire({
                        text: `${selectedMajorId} `,
                        icon: undefined,
                        confirmButtonText: '',
                        confirmButtonColor: '#002F5A'
                      });
                }
            ).catch(
                (error)=>{
                    // console.log("postApply:",error);
                }
            )
        }

        if(login && (thisApply == false) && (clicked === false)){
            RateService.postApply(thisUser, selectedMajorId, thisApply).then(
                (response) =>{
                    // console.log("post selectedMajorId:", selectedMajorId);
                    // console.log("user id:", thisUser);
                    // window.location.reload();
                    Swal.fire({
                      text: `${selectedMajorId} `,
                      icon: undefined,
                      confirmButtonText: '',
                      confirmButtonColor: '#002F5A'
                    });
                }
            ).catch(
                (error)=>{
                    // console.log("postApply:",error);
                }
            )
        }

        //   
        setClicked(false);

            //   
        //   
        if(sessionStorage.getItem("user")!==null && sessionStorage.getItem("user")!==undefined){
            let tmp = Object.values(JSON.parse(sessionStorage.getItem("user")));
            // console.log("sessionLog:", tmp);
            let userId = tmp[0];
            setThisUser(userId);
            setLogin(true); 

            // console.log("user id:", userId);
            //06.01 
            //    
            RateService.getApplyInfo(userId).then(
                (response) =>{
                    //API   stdNum: , apply: boolean, majorName: DB , gpa: , change: boolean
                    setApplyInfo(response.data);
                    setThisApply(response.data.apply);
                    setValid(response.data.change);
                    // console.log("applyInfo data:", response.data);

                    if(response.data.apply == true){
                        //     
                        setSelectedMajorId(response.data.majorName);
                    }
                    
                }
            )
        }
        //  
        else{
            setLogin(false);
        }
    },[thisApply])

    //    
    const SelectMajorId = (e) =>{
        setSelectedMajorId(e.target.value);
        // console.log("selectedMajorId:", e.target.value) ;
    }

    //   
    const applyMajor = () => {
        //  
        if(!login){
            //Login()
            alert(" !");
            return;
        }

        setThisApply(true);
        setClicked(true);
        //  GPA 
        // modalShow();
    }

    // 
    const cancelApplyMajor = () =>{
        // (default => false)
        setThisApply(false);
        setClicked(false);

    }

    //   
    const modalClose = () => setShowModal(false);
    const modalShow = () => setShowModal(true);

    // 
    const putGpa = (e)=> {
        // 
        setThisGpa(e.target.value);   
            
    }

    const postApplyInfo = () => {
        //   majorName thisApply 
        setThisApply(true);
    }

    return (
        <>
            <div className="mainContainer">
                <div className="header"><Header showMenu={showMenu}/></div>
                    <BodyBlock className="mainBody">
                        <div className='container'>
                            <div className="selectCampus">
                                <div className="selectFlex">
                                    <div className="selectSeoul" id="seoul" onClick={selectCampus}></div>
                                    <div className="selectGlobal" id="global" onClick={selectCampus}></div>
                                </div>
                            </div>
                            <div className="filterBlock">
                                {
                                    !applyInfo?
                                    <></>:
                                    <Form.Select onChange={SelectMajorId}>
                                        {
                                            !applyInfo?  
                                            <option value="0"> </option>:
                                            <option key={applyInfo.majorName} value={applyInfo.majorName}>
                                            {applyInfo.majorName}
                                            </option>
                                        }
                                    </Form.Select>
                                }
                            </div>
                            <div className="majorBlock">
                                {
                                    !thisApply?
                                    <>
                                    <span>  </span><br/>
                                    <span>
                                              
                                    </span>
                                    </>:
                                    <>
                                        {
                                            !majorInfo?
                                            <></>:
                                            <>
                                                <ApplyChart majorName={selectedMajorId} applyNum={majorInfo.applyNum} totalNum={majorInfo.totalNum} />
                                                
                                                {   
                                                    //  &   
                                                    login?
                                                    <GPAChart majorName={selectedMajorId} averageGPA={majorInfo.avgGpa}/>:
                                                    <>
                                                        <GPAChart majorName={"false"} averageGPA={majorInfo.avgGpa}/>
                                                        <div className="noticeAvgGpa" >     </div>
                                                    </>
                                                }
                                            </>
                                        }
                                    </>
                                }
                            </div>
                            <div className="applyBlock">
                                {
                                    !thisApply?
                                    <Button type="button" className="applyButton" onClick={()=>navigate("/recommend")}>  </Button>:
                                    <>
                                        {
                                            login?
                                            <>
                                            {
                                                thisApply == false && applyInfo.majorName != selectedMajorId?
                                                <Button type="button" className="applyButton" onClick={applyMajor}></Button>:
                                                <>
                                                {
                                                          valid == false?
                                                          <>
                                                           <OverlayTrigger
                                                              key='wait'
                                                              placement='top'
                                                              overlay={
                                                                  <Tooltip id="wait">
                                                                  <strong>  6  .</strong><br/>
                                                                  <span>  </span>
                                                                  </Tooltip>
                                                              }
                                                              >
                                                                  <div>
                                                                      <Button type="button"  className="appliedButton" variant="secondary"  disabled></Button>
                                                                      <br/>
                                                                      <small>{applyInfo.majorName}  .<br/>  </small>    
                                                                  </div>
                                                              </OverlayTrigger>
                                                              
                                                          </>:
                                                          <>
                                                              <Button type="button" className="appliedButton" variant="secondary" onClick={cancelApplyMajor}></Button>
                                                              <br/>
                                                              <small>{applyInfo.majorName}  .<br/>   .</small><br/>
                                                          </>
                                                      }   
                                                          
                                                      </>
                                                  }
                                            </>:
                                            <>
                                            <span className="warning"> , </span><br/>
                                            <Button type="button" className="applyButton" onClick={()=>navigate("/login")}>Login</Button>
                                            </>
                                        }
                                    </>
                                }

                            

                                
                                
                            </div>
                        </div>
                    </BodyBlock>
                <div className="footer"><Footer showPrev={showPrev} showNext={showNext} showDev={showDev}/></div>
            </div>
            <>
                <Modal show={showModal} onHide={modalClose}>
                <Modal.Header closeButton>
                    <Modal.Title><b>{selectedMajorId} </b></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>  </Form.Label>
                        <Form.Control
                        type="text"
                        placeholder=" ."
                        value={thisGpa}
                        onChange={putGpa}
                        autoFocus
                        />
                    </Form.Group>
                    </Form>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={modalClose}>
                    
                    </Button>
                    <Button variant="dark" onClick={postApplyInfo}>
                    
                    </Button>
                    
                </Modal.Footer>
                </Modal>
            </>
        </>
    );
    }
export default ShowMyRateInfo;

//CSS
const BodyBlock = styled.div`
    .container{
        display: grid;
        grid-template-rows: 1fr 1fr 5fr 1fr;
        background-color: white;
        text-align: center;
        /*justify-content: center;*/
        
        
        vertical-align: middle;
        row-gap: 10px;

        height: 70vh;
        width: 45vh;
    }
    

    /* */
    .selectCampus{
        grid-row-start: 1;
        grid-row-end: 2;

        font-weight: bold;
        font-size: 18px;

    }

    /*flex block*/
    .selectFlex{
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        align-items: stretch;
        
        color: white;
    }

    .selectedMajor{
        flex-grow: 1;
        padding: 5px;
        background-color: #875100;
        opacity: 0.57;
    }

    /*  */
    .filterBlock{
        grid-row-start: 2;
        grid-row-end: 3;

        font-weight: normal;
        font-size: 15px;
    }

    /* */
    .majorBlock{
        grid-row-start: 3;
        grid-row-end: 4;

        border: 1px solid #C4C4C4;

        .noticeAvgGpa{
            color: #028799;
            opacity:0.9;

            font-size: 12px;
        }
    }



    /*  */
    .applyBlock{
        grid-row-start:4;
        grid-row-end:5;
        // grid-template-rows: repeat(auto-fit, minmax(300px, auto));

        font-weight: bold;
        font-size: 18px;

        
        //  
        // display: flex;
        justify-content: center;
        align-items: center;

        // padding-top: 5%;

        .warning{
            font-size: 10px;
        }

        .applyButton{

            background-color: #875100;
            opacity: 0.57;
        
            /**/
            border-radius: 5px;
            width: 40%;
        
            /**/
            font-size: 14px;
            color: white;
            font-weight: bold;
    
            /**/
            &:hover {
                background-color: #875100;
                opacity: 0.8;
            }
        }

        .appliedButton{
            
            // background-color: #875100;
            // opacity: 0.8;
        
            /**/
            border-radius: 5px;
            width: 40%;
        
            /**/
            font-size: 14px;
            color: white;
            font-weight: bold;
    
            // /**/
            // &:hover {
            //     background-color: #875100;
            //     opacity: 0.9;
            // }
        }
    }

    #applyButton{

        background-color: #875100;
        opacity: 0.57;
    
        /**/
        border-radius: 5px;
        width: 40%;
    
        /**/
        font-size: 14px;
        color: white;
        font-weight: bold;

        /**/
        &:hover {
            background-color: #875100;
            opacity: 0.8;
        }
    }
`    