import React from 'react';
import { useState, useEffect, useRef } from "react";
import { useNavigate } from 'react-router-dom';
// 
import Form from 'react-validation/build/form';
import Input from 'react-validation/build/input';
import CheckButton from "react-validation/build/button";
//
import {Button,Container,Row,Col,Modal,Tooltip,OverlayTrigger} from 'react-bootstrap';
import Select from 'react-bootstrap/FormSelect'//bootstrap   Select (  Form.select    )
//
import Swal from 'sweetalert2' 
//API
import AuthService from '../../services/auth.service';
// 
import "../main/PMainHeader.css";
import "../main/PMainFrame.css";
import "./PUserInfo.css";
//  
import ShowContract from '../../page/user/signup/component/ShowContract';

/**   */
//input    
const required = (value) => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
         !
      </div>
    );
  }
};

//stdNum()
const vuserstdNum = (value) => {
  if (value.length < 4 || value.length > 9) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
         / .
      </div>
    );
  }
};

//nickName
const vusername = (value) => {
  if (value.length < 2 || value.length > 10) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
         4~10 .
      </div>
    );
  }
};

//password
const vpassword = (value) => {
  if (value.length < 6 || value.length > 20) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
         6~20 .
      </div>
    );
  }
};

//gpa
const vgpa = (value) => {
  if (value <= 0 || value > 4.5) {
    return (
      <div className="alert alert-danger" role="alert" style={{fontSize: "10px"}}>
          .
      </div>
    );
  }
};

function PSignup(){
    /**  */
    //     
    const [recommandService, setRecommandService] = useState(false);
    const [predictedRate, setPredictedRate] = useState(false);
    const [majorInfo, setMajorInfo] = useState(false);
    const [serviceIntro, setServiceIntro] = useState(false);

    /**  */
    const [totalFirstMajor, setTotalFirstMajor] = useState(''); //     
    const [totalDualMajor, setTotalDualMajor] = useState(''); //     
    const [userType, setUserType] = useState('mentee'); //   
    const [dualmajor, setDualmajor] = useState('');//      
    const [show, setShow] = useState(false);//  
    const [confirm, setConfirm] = useState(true); //  
  
    const form = useRef();
    const checkBtn = useRef();
    const [username, setUsername] = useState("");
    const [userstdNum, setUserstdNum] = useState("");
    const [password, setPassword] = useState("");
    const [grade, setGrade] = useState("1");
    const [firstMajor,  setFirstMajor] = useState(false);
    const [dualMajor, setDualMajor] = useState(false);
    const [gpa, setGpa] = useState(false);
    const [successful, setSuccessful] = useState(false);
    const [message, setMessage] = useState("");
  
    const [checkStdNum, setCheckStdNum] = useState('');
  

    //   
    let navigate = useNavigate();

    /**    */
    //    
    const handleSelectService = (type, state) => {
        
        //     
        if(!type){
            resetSelectedTab();
        }
        else if(type === "r"){
            //     
            selectRecommandService(state);
            showPageMovePopUp("  ");
        }
        else if(type === "p"){
            //     
            selectsetPredictedRate(state);
            showPageMovePopUp(" ");
        }
        else if(type === "m"){
            //     
            selectMajorInfo(state);
            showPageMovePopUp("  ");
        }
        else if(type === "i"){
            //     
            selectServiceIntro(state);
            showPageMovePopUp(" ");
        }
    }

    //    css  
    const resetSelectedTab = () =>{
        setRecommandService(false);
        setPredictedRate(false);
        setMajorInfo(false);
        setServiceIntro(false);        
    }

    const selectRecommandService = (state) =>{
        let reverseState = false;

        setRecommandService(state);
        setPredictedRate(reverseState);
        setMajorInfo(reverseState);
        setServiceIntro(reverseState);
    }

    const selectsetPredictedRate = (state) =>{
        let reverseState = false;

        setRecommandService(reverseState);
        setPredictedRate(state);
        setMajorInfo(reverseState);
        setServiceIntro(reverseState);
    }

    const selectMajorInfo = (state) =>{
        let reverseState = false;

        setRecommandService(reverseState);
        setPredictedRate(reverseState);
        setMajorInfo(state);
        setServiceIntro(reverseState);
    }

    const selectServiceIntro = (state) =>{
        let reverseState = false;

        setRecommandService(reverseState);
        setPredictedRate(reverseState);
        setMajorInfo(reverseState);
        setServiceIntro(state);
    }

    /**     */
    const showPageMovePopUp = (type, url="/") =>{
      Swal.fire({
        text: `"${type}"() ?`,
        icon: undefined,
        confirmButtonText: '',
        confirmButtonColor: '#145f7a',
        showCancelButton: true,
        cancelButtonText: ''
      }).then((result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          navigate(url)
        } else if (result.isDenied) {
          return
        }
      });
    }

    /**    */
    const onChangeUsername = (e) => {
      const username = e.target.value;
      setUsername(username);
    };
  
    const onChangeUserstdNum = (e) => {
      const userstdNum = e.target.value;
      setUserstdNum(userstdNum);
      
    };
  
    const onChangePassword = (e) => {
      const password = e.target.value;
      setPassword(password);
    };
  
    //   
    //select    
    const onChangeUserGrade = (e) => {
      const userGrade = e.target.value;
      setGrade(userGrade);
    }
  
    const onChangeUserGpa = (e) => {
      const userGpa = e.target.value;
      setGpa(userGpa);
    }
  
    const SelectedUserType= (selected) => {
      //   
      if(selected.target.value === "mento"){
        setUserType("mento");
        setDualmajor('()');
      }
  
      //   
      else{
        setUserType("mentee");
        setDualmajor('');
      }
    }
  
    const onChangeUserFirstMajor = (e) =>{
      const userFirstMajor = e.target.value;
  
      //     
      if(userFirstMajor !== dualMajor){
        setFirstMajor(userFirstMajor);
        return;
      }
      setFirstMajor(false);
  
      Swal.fire({
        text: "    ",
        icon: undefined,
        confirmButtonText: '',
        confirmButtonColor: '#002F5A'
      });
    }
  
    const onChangeUserDualMajor = (e) =>{
      const userDualMajor = e.target.value;
  
      //     
      if(userDualMajor !== firstMajor){
        setDualMajor(userDualMajor);
        return;
      }
      setDualMajor(false);
  
      Swal.fire({
        text: "    ",
        icon: undefined,
        confirmButtonText: '',
        confirmButtonColor: '#002F5A'
      });
    }
  
    //stdNum 
    const stdNumCheckDuplicate = () => {
      //stdNum   
      if(userstdNum === ''){
        required(userstdNum);
      }
      //stdNum    
      else if(userstdNum.length < 4 || userstdNum.length > 9){
        vuserstdNum(userstdNum);
      }
      //stdNum   
      else{
        AuthService.checkDuplicate(userstdNum).then(
          (response) => {
            // stdNum  
            // console.log(response.data.joinPossible)
            
            if(response.data.joinPossible === true){
              setCheckStdNum(true);
  
              Swal.fire({
                text: " /.",
                icon: undefined,
                confirmButtonText: '',
                confirmButtonColor: '#002F5A'
              });
            }
            else
              Swal.fire({
                text: "  /.",
                icon: undefined,
                confirmButtonText: '',
                confirmButtonColor: '#002F5A'
              });
          },
          (error) => {
            Swal.fire({
              text: " .",
              icon: undefined,
              confirmButtonText: '',
              confirmButtonColor: '#002F5A'
            });
          }
        );
      }
    }

    /** API  */
    const handleRegister = (e) => {
      e.preventDefault();
      setMessage("");
      setSuccessful(false);
      form.current.validateAll();
      
      ///   
      if (checkStdNum === false){
        Swal.fire({
          text: "/  .",
          icon: undefined,
          confirmButtonText: '',
          confirmButtonColor: '#002F5A'
        });
        return;
      }
  
      //  
      if(firstMajor === false){
        Swal.fire({
          text: " .",
          icon: undefined,
          confirmButtonText: '',
          confirmButtonColor: '#002F5A'
        });
        return;
      }
  
      //  
      if(dualMajor === false){
        Swal.fire({
          text: " .",
          icon: undefined,
          confirmButtonText: '',
          confirmButtonColor: '#002F5A'
        });
        return;
      }
  
      if(firstMajor === dualMajor){
        Swal.fire({
          text: "    ",
          icon: undefined,
          confirmButtonText: '',
          confirmButtonColor: '#002F5A'
        });
        return;
      }
  
      if (checkBtn.current.context._errors.length === 0) {
        AuthService.register(userstdNum, password, username, grade, userType, firstMajor, dualMajor, gpa).then(
          (response) => {
            setMessage(response.data.message);
            setSuccessful(true);
            
            let newUser = {"stdNum":userstdNum, "nickName": username, "grade": grade, "userType": userType, "firstMajor": firstMajor, "dualMajor": dualMajor, "gpa": gpa};
            // 
            sessionStorage.setItem("user", JSON.stringify(newUser));
  
            //   
            Swal.fire({
              text: " ?  ",
              icon: undefined,
              confirmButtonText: '',
              confirmButtonColor: '#002F5A'
            });
  
            //main page 
            navigate("/");
            window.location.reload();
          },
          (error) => {
            const resMessage =
              "  ."
              // (error.response &&
              //   error.response.data &&
              //   error.response.data.message) ||
              // error.message ||
              // error.toString();
            setMessage(resMessage);
            setSuccessful(false);
          }
        );
      }
    };

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
  
    /**   (  API  ) */
    // useEffect(  () =>{
    //   //  /    
    //   AuthService.firstMajorList();
    //   AuthService.dualMajorList();
  
    //   let allFirstMajor = false;
    //   let allDualMajor = false;
    //   if(localStorage.getItem('firstMajor') !== null){
    //       allFirstMajor = Object.values(JSON.parse(localStorage.getItem('firstMajor')));
    //   }
    //   if(localStorage.getItem('dualMajor') !== null){
    //       allDualMajor = Object.values(JSON.parse(localStorage.getItem('dualMajor')));
    //   }
  
      
    //   //   
    //   setTotalFirstMajor(allFirstMajor);
    //   //   
    //   setTotalDualMajor(allDualMajor);   
      
      
    //   //  
    //   setFirstMajor(allFirstMajor[0].id);
    //   setFirstMajor(allDualMajor[0].id);
  
    //   //       
    //   // setTotalFirstMajor(exampleFirstMajor);
    //   // setTotalDualMajor(exampleDualMajor);
    // },[])
  

    return (
        <div>
            {/* Header */}
            <div className='main-header'>
                <div className='main-icon' onClick={()=>navigate('/')}>
                    <img id='hufs-icon-white'src={require('../../media/main/().gif')} alt=" "/>
                    <span id='main-name'> ?</span>
                </div>
                <div className='main-select-service-wrap'>
                    {
                        !recommandService?
                        <div className='main-select-service-tab'>
                            <span onClick={()=>handleSelectService('r', true)}></span>
                        </div>:
                        <div className='selected-main-select-service'>
                            <span onClick={()=>handleSelectService('r', false)}></span>
                        </div>
                    }

                    {
                        !predictedRate?
                        <div className='main-select-service-tab'>
                            <span onClick={()=>handleSelectService('p', true)}></span>
                        </div>:
                        <div className='selected-main-select-service'>
                            <span onClick={()=>handleSelectService('p', false)}></span>
                        </div>
                    }

                    {
                        !majorInfo?
                        <div className='main-select-service-tab'>
                            <span onClick={()=>handleSelectService('m', true)}></span>
                        </div>:
                        <div className='selected-main-select-service'>
                            <span onClick={()=>handleSelectService('m', false)}></span>
                        </div>
                    }

                    {
                        !serviceIntro?
                        <div className='main-select-service-tab'>
                            <span onClick={()=>handleSelectService('i', true)}> </span>
                        </div>:
                        <div className='selected-main-select-service'>
                            <span onClick={()=>handleSelectService('i', false)}> </span>
                        </div>
                    }
                </div>
                <div className='login-wrap'>
                    {/*      */}
                </div>
            </div>
            {/* //Header */}

            {/* Main */}
            <div className='main-wrap'>
              <Container className="container-wrap">
                <Form onSubmit={handleRegister} ref={form}>
                {!successful && (
                  <div>
                  <Row className='main-row'>
                    <Col className="main-tit-wrap" lg={12} md={12} xs={8}>
                      <span class="main-tit">
                        ,<br/>
                            ! 
                      </span>
                    </Col>
                  </Row>
                  <Row className='main-row'>
                    <Col lg={2} md={0} xs={0}/>
                    {/* /  */}
                    <Col  lg={3} md={4} xs={5}>
                      <OverlayTrigger
                        key='stdNumInfo'
                        placement='top'
                        overlay={
                          <Tooltip id="stdNumInfo">
                              
                          </Tooltip>
                        }
                      >
                        <label className='input-label' htmlFor='userstdNum'>/</label>
                      </OverlayTrigger>
                      <Input 
                        type="userstdNum"
                        className="form-control"
                        name="userstdNum"
                        id="userstdNum"
                        value={userstdNum}
                        onChange={onChangeUserstdNum}
                        validations={[required, vuserstdNum]}
                      />
                    </Col>
                    <Col lg={2} md={2} xs={4}>
                      <div className='check-dup-btn-wrap'>
                        <Button type="button" className='check-btn check-dup' onClick={stdNumCheckDuplicate}></Button>
                      </div>
                    </Col>
                    {/*   */}
                    <Col lg={3} md={4} xs={8}>
                      <label className='input-label' htmlFor='firstMajor'></label>
                      <Select className='inputStyle' id="firstMajor" onChange={onChangeUserFirstMajor}>
                      {
                        !totalFirstMajor?  
                        <option value="0"> </option>:
                        totalFirstMajor.map(thisMajor => (
                          <option key={thisMajor.id} value={thisMajor.id}>
                            {thisMajor.name}
                          </option>
                        ))
                      }
                      </Select>
                    </Col>
                    <Col lg={2} md={2} xs={2}/>
                  </Row>
                  <Row className='main-row'>
                    <Col lg={2} md={0} xs={0}/>
                    {/*  */}
                    <Col lg={3} md={4} xs={5}>
                      <label className='input-label' htmlFor='username'></label>
                      <Input 
                        type="username"
                        className="form-control"
                        name="username"
                        id="username"
                        value={username}
                        onChange={onChangeUsername}
                        validations={[required, vusername]}
                      />
                    </Col>
                    <Col lg={2} md={2} xs={4}></Col>
                    {/*  */}
                    <Col lg={3} md={4} xs={8}>
                      <label className='input-label' htmlFor='usergrade'></label>
                      <Select className='inputStyle' id="usergrade" onChange={onChangeUserGrade}>
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4 ">4 </option>
                    </Select>
                    </Col>
                    <Col lg={2} md={2} xs={2}/>
                  </Row>
                  <Row className='main-row'>
                    <Col lg={2} md={0} xs={0}/>
                    {/*   */}
                    <Col lg={3} md={4} xs={5}>
                      <label className='input-label' htmlFor='password'></label>
                      <Input
                        type="password"
                        className="form-control"
                        name="password"
                        id="password"
                        value={password}
                        onChange={onChangePassword}
                        validations={[required, vpassword]}
                      />
                    </Col>
                    <Col lg={2} md={2} xs={4}></Col>
                    {/*   */}
                    <Col lg={3} md={4} xs={8}>
                    <OverlayTrigger
                        key='stdNumInfo'
                        placement='top'
                        overlay={
                          <Tooltip id="stdNumInfo">
                            <br/>
                            : ()  .<br/>
                            :  () .
                          </Tooltip>
                        }
                      >
                        <label className='input-label' htmlFor='userType'></label>
                      </OverlayTrigger>
                      <Select className='inputStyle' id="userType" onChange={SelectedUserType}>
                        <option value="mentee"></option>
                        <option value="mento"></option>
                      </Select>
                    </Col>
                    <Col lg={2} md={2} xs={2}/>
                  </Row>
                  <Row className='main-row'>
                    <Col lg={2} md={0} xs={0}/>
                    {/*  */}
                    <Col lg={3} md={4} xs={5}>
                      <div>
                        <span className='input-label'></span>
                        <Button className='check-btn term-service-btn' onClick={handleShow}></Button>
                      </div>
                      <br/>
                    </Col>
                    <Col lg={2} md={2} xs={4}></Col>
                    {/* /()  */}
                    <Col lg={3} md={4} xs={8}>
                      <label className='input-label' htmlFor='dualMajor'>{dualmajor}</label>
                      <Select className='inputStyle' id="dualMajor" onChange={onChangeUserDualMajor}>
                      {
                        !totalDualMajor?  
                        <option value="0"> </option>:
                        totalDualMajor.map(thisMajor => (
                          <option key={thisMajor.id} value={thisMajor.id}>
                            {thisMajor.name}
                          </option>
                        ))
                      }
                      </Select> 
                    </Col>
                    <Col lg={2} md={2} xs={2}/>
                  </Row>
                  <Row>
                    <Col lg={7} md={6} xs={8}/>
                    {/*   */}
                    <Col lg={3} md={4} xs={8}>
                      <label className='input-label' htmlFor="averageGPA"> </label>
                      <Input
                        type="number" 
                        step="0.01"
                        className="form-control"
                        name="gpa"
                        id="averageGPA"
                        value={gpa}
                        onChange={onChangeUserGpa}
                        validations={[required, vgpa]}
                      />
                    </Col>
                    <Col lg={2} md={2} xs={2}/>
                  </Row>
                  </div>
                  )}
                  <Row>
                    <Col className="notice-wrap" lg={12} md={12} xs={8}>
                        <span class="notice-style">
                          *  .
                        </span>
                        <br/>
                        <Button type="submit" className="confirm-btn" ref={checkBtn} disabled={confirm}></Button>
                    </Col>
                  </Row>
                  {/*      */}
                  {message && (
                    <div className="form-group">
                      <div
                        className={ successful ? "alert alert-success" : "alert alert-danger" }
                        role="alert"
                      >
                        {message}
                      </div>
                    </div>
                  )}
                  <CheckButton style={{ display: "none" }} ref={checkBtn} />
                  {/* //     */}
                </Form>
              </Container>
            </div>
            {/* //Main */}

            {/* Modal */}
            <Modal show={show} fullscreen={true} onHide={handleClose}>
              <Modal.Header closeButton>
                <Modal.Title></Modal.Title>
              </Modal.Header>
              <Modal.Body><ShowContract/></Modal.Body>
              <Modal.Footer>
                <Button className="withdrawal-btn" onClick={ () => {
                  handleClose();
                  setConfirm(true);
                }}>
                  
                </Button>
                <Button className="confirm-btn" onClick={() => {
                  handleClose();
                  setConfirm(false);
                }}>
                  
                </Button>
              </Modal.Footer>
            </Modal>
            {/* //Modal */}
        </div>
    );
};

export default PSignup;