// () component
import React from 'react'
import styled from 'styled-components'
import ReactTypingEffect from 'react-typing-effect';
import { Table } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2'

export default function MainFrame() {

    //   callback 
    let navigate = useNavigate();

    //   
    const moveToRecommend = () => {
        //question page 
        navigate("/recommend");
        window.location.reload();
    }

    //  
    const moveToRate = () => {
        //RateMain 
        navigate("/rate");
        window.location.reload();
    }

    //   
    const moveToMajorDetail = () => {
        navigate("/seoulMajorInfo");
        window.location.reload();
    }

    //    
    const moveToMyMajorInfo = () => {
        navigate("/showMyMajorInfo");
        window.location.reload();
    }

    const firstNotice = () => {
        Swal.fire({
            text: `mbti   , \n   \n ~\n  '' \n   \n  !`,
            icon: undefined,
            confirmButtonText: '',
            confirmButtonColor: '#002F5A'
          });
    }

    const secondNotice = () => {
        Swal.fire({
            text: `   ,\n    \n   \n  \n    \n~`,
            icon: undefined,
            confirmButtonText: '',
            confirmButtonColor: '#002F5A'
        });
    }

    return (
    <>
        <BodyBlock>
            <div className='container'>
                <div className='choose'>
                    <ChooseBlock>
                        <div className='containerFlex'>
                            <div className='recommend' onClick={moveToRecommend}><img src={require('../../../media/tab/.png')} alt=""/><br/><br/><span> </span></div>
                            <div className='compete' onClick={moveToRate}><img src={require('../../../media/tab/.png')} alt=""/><br/><br/><span>&nbsp; &nbsp;</span></div>
                        </div>
                    </ChooseBlock>
                </div>
                <div className='write' onClick={moveToMajorDetail}>  </div>
                <div className='review' onClick={moveToMyMajorInfo}>  </div>
                <div className='notice'>
                    <span><b></b></span>
                    <br/>
                    <small id="noticeItem" onClick={firstNotice}>1.   </small>
                    <br/>
                    <small id="noticeItem" onClick={secondNotice}>2.     </small>
                </div>
            </div>
        </BodyBlock>
    </>
  )
}
 //CSS
 const BodyBlock = styled.div`
 div.container{
     display: grid;
     grid-template-rows: 3fr 1fr 1fr 5fr;
     background-color: white;
     text-align: center;
     /*justify-content: center;*/
     
     vertical-align: middle;
     row-gap: 10px;

     height: 70vh;
     width: 45vh;
 }

 /* */
 

 /*  &  */
 div.choose{
     grid-row-start: 0;
     grid-row-start: 1;

     grid-template-rows: repeat(auto-fit, minmax(300px, auto));
 }

 /* */
 div.write{
     grid-row-start: 1;
     grid-row-start: 2;
     grid-template-rows: repeat(auto-fit, minmax(300px, auto));

     /**/
     background-color: #875100;
     opacity: 0.57;

     /**/
     border-radius: 10px;
     

     /**/
     padding-top: 3%;
     font-size: 20px;
     color: white;
     font-weight: bold;

     /**/
     &:hover {
         background-color: #875100;
         opacity: 0.67;
       }
 }

 /* */
 div.review{
     grid-row-start: 2;
     grid-row-start: 3;    

     /**/
     background-color: #875100;
     opacity: 0.8;

     /**/
     border-radius: 10px;

     /**/
     padding-top: 3%;
     font-size: 20px;
     color: white;
     font-weight: bold; 
     
     /**/
     &:hover {
         background-color: #875100;
         opacity: 0.9;
       }
 }

 /**/
 div.notice{
     grid-row-start: 3;
     grid-row-start: 4;

     /**/
     background-color: white;
     border: solid 1px #5a5a5a;

     /**/
     border-radius: 10px;

     /**/
     font-size: 15px;
     color: #5a5a5a;
     opacity: 0.8;
     font-weight: normal;
 }

 #noticeItem{
    /**/
    &:hover {
        color: black;
        font-weight: bold;
    }
 }

`


const ChooseBlock = styled.div`
 div.containerFlex{
     min-height: 24vh;
     display: flex;
     flex-direction: row;
     flex-wrap: nowrap;
     align-items: stretch;
     background-color: white;

 }

 div.recommend{
     flex-grow: 1;
     align-self: stretch;

     /**/
     background-color: #002F5A;
     opacity: 0.8;

     /**/
     margin-right: 5px;
     border-radius: 10px;

     /**/
     padding-top: 10%;
     font-size: 19.5px;
     color: white;
     font-weight: bold;

     /**/
     &:hover {
         background-color: #002F5A;
         opacity: 0.9;
       }

     /**/
     img{
         width: 45px;
         height: 45px;
     }
 }

 div.compete{
     flex-grow: 1;
     align-self: stretch;
     
     /**/
     background-color: #028799;
     opacity: 0.9;

     /**/
     margin-left: 5px;
     border-radius: 10px;

     /**/
     padding-top: 10%;
     font-size: 19.5px;
     color: white;
     font-weight: bold;

     /**/
     &:hover {
         background-color: #028799;
         opacity: 1;
       }
     
     /**/
     img{
         width: 45px;
         height: 45px;
     }
 }
`



