import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { Button, Accordion, ListGroup} from 'react-bootstrap';
import RecommendService from '../../../services/recommend.service';
import { useNavigate } from 'react-router-dom';
//import ReactTooltip from 'react-tooltip';

export default function Result() {
    // 
    const [thisResult, setThisResult] = useState(false); //  
    const [isError, setIsError] = useState(false); //    
    const [answer, setAnswer] = useState(false); //   
    const [testKey, setTestKey] = useState(false);

    //   callback 
    let navigate = useNavigate();

    //  
    useEffect(() => {
        // () 
        RecommendService.getTestKey().then(
            (response) => {
                setTestKey(response.data);
            }
        )
    },[])

    //   
    useEffect(() => {

        //  
        let departmentName = localStorage.getItem('recommendResult');

        //     
        RecommendService.getDepartmentInfo(departmentName).then(
            (response) => {

                // console.log("getData:", response.data)

                //   
                setThisResult(response.data);
                //
                // ShowResult();
            }
        ).catch(
            (Error) => {
                //  
                // setIsError(true);
            }
        )
        //
        //setThisResult(testData.info);
        //thisResult    

        ShowResult();
    },[testKey])

    useEffect(() => {
        ShowResult();
    },[thisResult])
    
    const ShowResult = () => {
        // console.log('thisResult:',thisResult);
        // console.log('testData.list.academicName:',thisResult[0]);

        if(!thisResult){
            return(
                <></>
            );
        }
        return(
            <>
                <Accordion defaultActiveKey="0" flush style={{width:"100%"}}>
                    {
                        //testData.info.map(thisData => (
                            <>
                                <Accordion.Item eventKey={thisResult.departmentName}>
                                    <Accordion.Header>{thisResult.departmentName}</Accordion.Header>
                                    <Accordion.Body>
                                    <ListGroup>
                                        {
                                            (thisResult.campus !== null)?
                                            <ListGroup.Item>
                                                <div className="fw-bold"></div><br/>
                                                {thisResult.campus}</ListGroup.Item>:
                                            <></>
                                        }
                                        {
                                            (thisResult.intro !== null)?
                                            <ListGroup.Item>
                                                <div className="fw-bold"></div><br/>
                                                {thisResult.intro}</ListGroup.Item>:
                                            <></>
                                        }
                                        {
                                            (thisResult.degree !== null)?
                                            <ListGroup.Item>
                                                <div className="fw-bold"></div><br/>
                                                {thisResult.degree}</ListGroup.Item>:
                                            <></>
                                        }
                                        {
                                            (thisResult.career !== null)?
                                            <ListGroup.Item>
                                                <div className="fw-bold"></div><br/>
                                                {thisResult.career}</ListGroup.Item>:
                                            <></>
                                        }
                                        {
                                            (thisResult.curriculum !== null)?
                                            <ListGroup.Item>
                                                <div className="fw-bold"> </div><br/>
                                                {thisResult.curriculum}</ListGroup.Item>:
                                            <></>
                                        }
                                        {
                                            (thisResult.certification!== null)?
                                            <ListGroup.Item>
                                                <div className="fw-bold"> </div><br/>
                                                {thisResult.certification}</ListGroup.Item>:
                                            <></>                                                   
                                        }
                                        {
                                            (thisResult.webPage !== null)?
                                            <ListGroup.Item>
                                                <div className="fw-bold"></div><br/>
                                                <a href={`${thisResult.webPage}`} target="_blank" rel="noreferrer">
                                                {thisResult.webPage}</a></ListGroup.Item>:
                                            <></>
                                        }
                                        {
                                            (thisResult.phoneNum !== null)?
                                            <ListGroup.Item>
                                                <div className="fw-bold"> </div><br/>
                                                <a href={`tel:${thisResult.phoneNum}`}>
                                                    {thisResult.phoneNum}</a></ListGroup.Item>:
                                            <></>
                                        }
                                    </ListGroup>
                            </Accordion.Body>
                        </Accordion.Item>
                    </>
                
                    }
                </Accordion>
            </>
        )
    }

    const testAgain = () => {
        navigate("/recommend");
    }
 
  return (
    <BodyBlock>
        <div className="container">
            <div className='notice'>
                <span><b>!!   !!</b></span><br/>
            </div>
            <div className='resultFrame'>
                {
                    !thisResult?
                    <></>:
                    <ShowResult/>  
                }
            </div>
            <div className='nextButtonFrame'>
            <Button className='nextButton' onClick={testAgain}> </Button>
            </div>
        </div>
    </BodyBlock>
  )
}

//CSS
const BodyBlock = styled.div`
    .container{
        display: grid;
        grid-template-rows: 1fr 5fr 1fr;
        background-color: white;
        text-align: center;
        /*justify-content: center;*/
        
        
        /*vertical-align: middle;*/
        row-gap: 10px;

        // height: 70vh;
        width: 45vh;
    }
    

    /**/
    .notice{
        grid-row-start: 1;
        grid-row-end: 2;

        /*grid-template-rows: repeat(auto-fit, minmax(300px, auto));*/

        align-items: center;
        
        /**/
        padding-top: 3%;
        font-size: 15px;
        color: #5a5a5a;

    }

    /**/
    .resultFrame{
        grid-row-start: 2;
        grid-row-end: 3;
        /*grid-template-rows: repeat(auto-fit, minmax(300px, auto));*/

        //  
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;

        .eachResult:nth-child(odd){

            background-color: #002F5A;
            opacity: 0.8;
        
            /**/
            border-radius: 5px;
            height: 3em;
            width: 50%;
        
            /**/
            font-size: 14px;
            color: white;
            font-weight: bold;
            padding-top: 10px;
    
            /**/
            &:hover {
                background-color: #002F5A;
                opacity: 1;
            }
        }

        .eachResult:nth-child(even){

            background-color: #028799;
            opacity: 0.8;
        
            /**/
            border-radius: 5px;
            height: 3em;
            width: 50%;
        
            /**/
            font-size: 14px;
            color: white;
            font-weight: bold;
            padding-top: 10px;
    
            /**/
            &:hover {
                background-color: #028799;
                opacity: 1;
            }
        }
    }

   
    /* */
    .nextButtonFrame{
        grid-row-start: 3;
        grid-row-end: 4;
        /*grid-template-rows: repeat(auto-fit, minmax(300px, auto));*/

        //  
        display: flex;
        justify-content: center;
        align-items: center;

        .nextButton{

            background-color: #002F5A;
            opacity: 0.8;
        
            /**/
            border-radius: 5px;
            width: 70%;
        
            /**/
            font-size: 14px;
            color: white;
            font-weight: bold;
    
            /**/
            &:hover {
                background-color: #002F5A;
                opacity: 1;
            }
        }
    }
`