//  header (  )
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import 'bootstrap/dist/css/bootstrap.css';
import MyModal from './MyModal';
import axios from 'axios'
import '../../../media/css/header.css';

export default function Header({showMenu}) {
  //  ( :  false)
  const [login, setLogin] = useState(false);
  const [thisUser, setThisUser] = useState('');

  const [modalShow, setModalShow] = useState(false); //     

  //   
  useEffect( () =>{
    if(sessionStorage.getItem("user")!=null){
      setLogin(true);
    }
    else{
      setLogin(false);
    }
  })

  //   
  function ChooseOption(){
    // x (false)
    if( login === false ){
      // or   
      // window.location.href='http://localhost:3000/#/choose';
      return(
      <div className='menu'>
        <Link style={{textDecoration: 'none' }} to={'/choose'}>
          <img src={require('../../../media/tab/.png')} alt=''/>
        </Link>
       </div>
      )
    }

    //   (true)
    else{
      return(
        //     userdata 
        <div className='menu' onClick={()=>MyModal()}>
          <img src={require('../../../media/tab/.png')} alt=''/>
        </div>
      )
    }
  }


  return (
    <>
      <div className='containerHeader'>
        <div className='logo'>
          <Link to={'/'}>
            <img src={require('../../../media/structure/_.jpg')} alt='' />
          </Link>
        </div>
        <div className='blank'>
          <Link className= 'blank' style={{textDecoration: 'none', color: '#002F5A'}} to={'/'}>
            <span> ?</span>
          </Link>
        </div>
        {
        !showMenu?
          <>
            <div className='menu'>
              <img src={require('../../../media/structure/.jpg')} alt=''></img>
            </div>
          </>:
          <>
            {
              login === false ?
              <div className='menu'>
                <Link style={{textDecoration: 'none' }} to={'/choose'}>
                  <img src={require('../../../media/tab/.png')} alt=''/>
                </Link>
              </div>
              :
              <div className='menu' onClick={()=> setModalShow(true)}>
                <img src={require('../../../media/tab/.png')} alt=''/>
              </div>
            }
          </>
        }
      </div>
      <MyModal show={modalShow} onHide={() => setModalShow(false)} />
    </>
  )
}
// login? MyModal(thisUser): location.href='http://localhost:3000/#/choose'

