//  footer (,     )
import React from 'react'
import styled from 'styled-components'
import '../../../media/css/footer.css';
import {OverlayTrigger, Tooltip} from 'react-bootstrap';
import Swal from 'sweetalert2';

export default function Footer({showPrev, showNext, showDev}) {

  
  //showDev(,  )
  //showPrev(  )
  //showNext(  )
  return (
      <div className='containerFooter'>
        {
          !showDev?
          <>
            {
              !showPrev?
              <>
                <div className='prev'><img src={require('../../../media/structure/.jpg')} alt=""/></div>
              </>:
              <>
                <div className='prev' onClick={() => { window.history.back(); }}><img src={require('../../../media/tab/.png')} alt=""/></div>
              </>
            }
            {
              !showNext?
              <>
                <div className='next'><img src={require('../../../media/structure/.jpg')} alt=""/></div>
              </>:
              <>
                <div className='next' onClick={() => { window.history.forward(); }}><img src={require('../../../media/tab/.png')} alt=""/></div>
              </>
            }
          </>:
          <>
              <div className='title'><span><b>/:</b></span><br/><span><b>:</b></span></div>
              <div className='content'><span>, , , </span><br/><a href="mailto:rangyun36@gmail.com">rangyun36@gmail.com</a></div>
          </>

        }
      </div>
  )
}
