import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { Button,  Modal, OverlayTrigger, Tooltip, Row, Col, Container, Accordion, ListGroup, InputGroup, FormControl} from 'react-bootstrap';
import RecommendService from '../../../services/recommend.service';
import { useNavigate, useParams } from 'react-router-dom';
//import ReactTooltip from 'react-tooltip';
import Swal from 'sweetalert2'   
// import MainFrame from "./MainFrame";

export default function Result() {
    // 
    const [thisResult, setThisResult] = useState(false); //  
    const [isError, setIsError] = useState(false); //    
    const [answer, setAnswer] = useState(false); //   


    //  
    const [modalShow, setModalShow] = useState(false); //   
    // const [satisfyingStar, setSatisfyingStar] = useState(1); //
    // const [reviewQuestion1, setReviewQuestion1] = useState("");// 1
    // const [reviewQuestion2, setReviewQuestion2] = useState("");// 2
    // const [reviewQuestion3, setReviewQuestion3] = useState("");// 3
    // const [reviewQuestion4, setReviewQuestion4] = useState("");// 4
    // const [reviewQuestion5, setReviewQuestion5] = useState("");// 5
    // const [reviewQuestion6, setReviewQuestion6] = useState("");// 6
    // const [reviewQuestion7, setReviewQuestion7] = useState("");// 7

    //
    // let testData = {
    //     "info": [
    //         {
    //             "departmentName": "",
    //             "campus": "",
    //             "intro": "inf1",
    //             "degree": "deg1",
    //             "career": "career1",//null
    //             "curriculum": "",//null
    //             "certification": "",//null
    //             "webPage": "www.hufs.ac.kr" //null
    //         },
    //         {
    //             "departmentName": "",
    //             "campus": "",
    //             "intro": "inf2",
    //             "degree": "deg2",
    //             "career": "career2",
    //             "curriculum": "",//null
    //             "certification": null,//null
    //             "webPage": "www.hufs.ac.kr    (WEF)       .      (CIO)  8000    .   70%      ."
    //         }
    //     ]
    // }

    //   callback 
    let navigate = useNavigate();


    useEffect(() => {
        //  
        let testKeyValidate = sessionStorage.getItem('testKey');
        let resultType = sessionStorage.getItem('result2Type');

        //     
        RecommendService.getFinalResult(resultType, testKeyValidate).then(
            (response) => {

                // console.log("getData:", response.data);

                //   
                setThisResult(response.data.info);
                //
                ShowResult();
            }
        ).catch(
            (Error) => {
                //  
                setIsError(true);
            }
        )

        //
        //setThisResult(testData.info);
        //thisResult    

        ShowResult();
    },[])

    // useEffect(() => {
    //     ShowResult();
    // },[thisResult])

    const goToStart = () => {
        //   page 
        navigate("/recommend");
        window.location.reload();
        
    }
    
    const ShowResult = () => {
        // console.log('thisResult:',thisResult);
        // console.log('testData.list.academicName:',thisResult[0]);

        if(!thisResult){
            return(
                <></>
            );
        }
    
      

        return(
                <>
                {
                    !thisResult[0].intro?
                    <>
                        {thisResult[0].departmentName}
                    </>:
                    <Accordion style={{width:"90%"}}>
                    {
                        //testData.info.map(thisData => (
                        thisResult.map(thisData => (
                            <>
                                <Accordion.Item eventKey={thisData.departmentName} style={{width:"100%"}}>
                                    <div id={`${thisData.departmentName}`} onClick={selectResult}>
                                        <Accordion.Header>{thisData.departmentName}</Accordion.Header>
                                    </div>
                                    <Accordion.Body>
                                        <ListGroup>
                                            {
                                                (thisData.campus !== null)?
                                                <ListGroup.Item>
                                                    <div className="fw-bold"></div><br/>
                                                    {thisData.campus}</ListGroup.Item>:
                                                <></>
                                            }
                                            {
                                                (thisData.intro !== null)?
                                                <ListGroup.Item>
                                                    <div className="fw-bold"></div><br/>
                                                    {thisData.intro}</ListGroup.Item>:
                                                <></>
                                            }
                                            {
                                                (thisData.degree !== null)?
                                                <ListGroup.Item>
                                                    <div className="fw-bold"></div><br/>
                                                    {thisData.degree}</ListGroup.Item>:
                                                <></>
                                            }
                                            {
                                                (thisData.career !== null)?
                                                <ListGroup.Item>
                                                    <div className="fw-bold"></div><br/>
                                                    {thisData.career}</ListGroup.Item>:
                                                <></>
                                            }
                                            {
                                                (thisData.curriculum !== null)?
                                                <ListGroup.Item>
                                                    <div className="fw-bold"> </div><br/>
                                                    {thisData.curriculum}</ListGroup.Item>:
                                                <></>
                                            }
                                            {
                                                (thisData.certification!== null)?
                                                <ListGroup.Item>
                                                    <div className="fw-bold"> </div><br/>
                                                    {thisData.certification}</ListGroup.Item>:
                                                <></>                                                   
                                            }
                                            {
                                                (thisData.webPage !== null)?
                                                <ListGroup.Item>
                                                    <div className="fw-bold"></div><br/>
                                                    <a href={`${thisData.webPage}`} target="_blank" rel="noreferrer">
                                                    {thisData.webPage}</a></ListGroup.Item>:
                                                <></>
                                            }
                                            {
                                                (!thisData.phoneNum === false)?
                                                <ListGroup.Item>
                                                    <div className="fw-bold"> </div><br/>
                                                    <a href={`tel:${thisData.phoneNum}`}>
                                                        {thisData.phoneNum}</a></ListGroup.Item>:
                                                <></>
                                            }
                                        </ListGroup>
                                    </Accordion.Body>
                                </Accordion.Item>
                            </>
                        ))
                    }
                </Accordion>
                }
            </>
        )
    }


    //get url
     
    let params = useParams(); //url parameter  
    let productId = params.id; //   
    

    const selectResult =(event) =>{
        let result = event.currentTarget.id;
        // let result = selectedElement.current.id;

        // console.log("selectedResult:",result);
        setAnswer(result);

        //    ( )
        document.getElementById(result).style.border = "1px solid #002F5A";
        document.getElementById(result).style.color = "white";
    }

    const saveData = () => {
        //      
        if(answer !== false){


            //  
            let testKeyValidate = sessionStorage.getItem('testKey');

            //  
            let thisUser = JSON.parse(sessionStorage.getItem("user"));

            //   false  
            if(!thisUser){
                thisUser = "false";
            }
            else{
                thisUser = thisUser.id;
                // console.log("thisUser:", thisUser);
            }
            // console.log("answer:",answer);

            //  
            let starRecord = sessionStorage.getItem('starCount');

            if(!starRecord){
                sessionStorage.removeItem('starCount')
            }

            //  
            let reviewQuestion1 = sessionStorage.getItem("ReviewQuestion1");
            let reviewQuestion2 = sessionStorage.getItem("ReviewQuestion2");
            let reviewQuestion3 = sessionStorage.getItem("ReviewQuestion3");
            let reviewQuestion4 = sessionStorage.getItem("ReviewQuestion4");
            let reviewQuestion5 = sessionStorage.getItem("ReviewQuestion5");
            let reviewQuestion6 = sessionStorage.getItem("ReviewQuestion6");
            let reviewQuestion7 = sessionStorage.getItem("ReviewQuestion7");

            //          ->    
            localStorage.setItem('recommendResult', answer);

            Swal.fire({
                text: ".\n        ",
                icon: undefined,
                confirmButtonText: '',
                confirmButtonColor: '#002F5A'
              });

            // API
            RecommendService.saveResult(answer, thisUser ,testKeyValidate).then(
                (response) => {
                }
            );


            //API
            RecommendService.saveSurvey(reviewQuestion1, reviewQuestion2, reviewQuestion3, reviewQuestion4, reviewQuestion5, reviewQuestion6, reviewQuestion7, starRecord, thisUser, testKeyValidate).then(
                (response) => {
                    navigate('/') //  
                }
            )
        }
        else{
            Swal.fire({
                text: "   ~",
                icon: undefined,
                confirmButtonText: '',
                confirmButtonColor: '#002F5A'
              });
        }
    }




 
    function SatisfactionModal(props) {
        const Star = () => {
            const drawStar = (e) => {
                let thisCount = e.target.value;
                document.getElementById("realStar").style.width = `${thisCount * 10}%`;

                sessionStorage.setItem("starCount",thisCount);
            }
    
          return (
            <>
                <StarFrame >
                    <label>
                        <span className="star">
                            
                            <span id="realStar"></span>
                                <input type="range" onChange={drawStar} value="1" step="1" min="0" max="10"/>
                        </span>
                    </label>
                </StarFrame>
            </>
          )
        }

        //   
        const selectReviewQuestion1 = (e) => {
            let thisReview = e.target.value;
            sessionStorage.setItem("ReviewQuestion1",thisReview);
            //setReviewQuestion1(thisReview);

        }

        const selectReviewQuestion2 = (e) => {
            let thisReview = e.target.value;
            sessionStorage.setItem("ReviewQuestion2",thisReview);
            // setReviewQuestion2(thisReview);
        }

        const selectReviewQuestion3 = (e) => {
            let thisReview = e.target.value;
            sessionStorage.setItem("ReviewQuestion3",thisReview);
            // setReviewQuestion3(thisReview);
        }

        const selectReviewQuestion4 = (e) => {
            let thisReview = e.target.value;
            sessionStorage.setItem("ReviewQuestion4",thisReview);
            // setReviewQuestion4(thisReview);
        }

        const selectReviewQuestion5 = (e) => {
            let thisReview = e.target.value;
            sessionStorage.setItem("ReviewQuestion5",thisReview);
            // setReviewQuestion5(thisReview);
        }

        const selectReviewQuestion6 = (e) => {
            let thisReview = e.target.value;
            sessionStorage.setItem("ReviewQuestion6",thisReview);
            // setReviewQuestion6(thisReview);
        }

        const selectReviewQuestion7 = (e) => {
            let thisReview = e.target.value;
            sessionStorage.setItem("ReviewQuestion7",thisReview);
            // setReviewQuestion7(thisReview);
        }
           
        return (
            <Modal {...props} aria-labelledby="contained-modal-title-vcenter">
                <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter" >
                    <Container>
                    <Row>
                        <Col md={12} xs={12} >
                        <h6><b>  ?</b></h6>
                        </Col>
                    </Row>
                    </Container>
                </Modal.Title>
                </Modal.Header>
                <Modal.Body className="show-grid">
                <Container>
                    <Row>
                    <Col xs={12} md={12}>
                        <small><b> </b></small>
                    </Col>
                    <Col xs={12} md={12}>
                        <Star/>
                    </Col>
        
                    <Col xs={12} md={12}>
                        <small><b>     ?</b></small>
                    </Col>
                    <Col xs={12} md={12}>
                        <InputGroup>
                            <FormControl onChange={(e) => selectReviewQuestion1(e)}  aria-label="Username"  aria-describedby="basic-addon1"  placeholder=" ~"></FormControl>
                        </InputGroup>
                    </Col>

                    <Col xs={12} md={12}>
                        <small><b>  () ?</b></small>
                    </Col>
                    <Col xs={12} md={12}>
                        <InputGroup>
                            <FormControl onChange={(e) => selectReviewQuestion2(e)}  aria-label="Username"  aria-describedby="basic-addon1"  placeholder="()  x ~"></FormControl>
                        </InputGroup>
                    </Col>

                    <Col xs={12} md={12}>
                        <small><b>   MBTI  ?</b></small>
                    </Col>
                    <Col xs={12} md={12}>
                        <InputGroup>
                            <FormControl onChange={(e) => selectReviewQuestion3(e)}  aria-label="Username"  aria-describedby="basic-addon1"  placeholder="ex: ENTJ"></FormControl>
                        </InputGroup>
                    </Col>

                    <Col xs={12} md={12}>
                        <small><b>       ?</b></small>
                    </Col>
                    <Col xs={12} md={12}>
                        <InputGroup>
                            <FormControl onChange={(e) => selectReviewQuestion4(e)}  aria-label="Username"  aria-describedby="basic-addon1"  placeholder="0 ~ 10   ~"></FormControl>
                        </InputGroup>
                    </Col>

                    <Col xs={12} md={12}>
                        <small><b>      </b></small>
                    </Col>
                    <Col xs={12} md={12}>
                        <InputGroup>
                            <FormControl onChange={(e) => selectReviewQuestion5(e)}  aria-label="Username"  aria-describedby="basic-addon1"  placeholder="   ~~"></FormControl>
                        </InputGroup>
                    </Col>

                    <Col xs={12} md={12}>
                        <small><b>      ?</b></small>
                    </Col>
                    <Col xs={12} md={12}>
                        <InputGroup>
                            <FormControl onChange={(e) => selectReviewQuestion6(e)}  aria-label="Username"  aria-describedby="basic-addon1"  placeholder="     "></FormControl>
                        </InputGroup>
                    </Col>

                    <Col xs={12} md={12}>
                        <small><b>     </b></small>
                    </Col>
                    <Col xs={12} md={12}>
                        <InputGroup>
                            <FormControl onChange={(e) => selectReviewQuestion7(e)}  aria-label="Username"  aria-describedby="basic-addon1"  placeholder="    ID ~"></FormControl>
                        </InputGroup>
                    </Col>
                        
                    <PersonalButton>
                        <Col xs={12} md={12}>
                        <br/>
                        <Button className='recommend' onClick={saveData}></Button>
                        </Col>
        
                        <Col xs={12} md={12}>
                        <Button className='compete' onClick={goToStart}> </Button>
                        </Col>
        
                    </PersonalButton>
                    </Row>
        
                </Container>
                </Modal.Body>
            </Modal>
        )
      }


  return (
    <BodyBlock>
        <div className="container">
            <div className='notice'>
                <span><b>!!   !!</b></span><br/>
                {/* <span>     sns   ~</span> */}
            </div>
            <div className='resultFrame'>
                {
                    !thisResult?
                    <></>:
                    <ShowResult/>  
                }
            </div>
            {
                !thisResult?
                <></>:
                <>
                {
                    !thisResult[0].intro?
                    <>
                        <span>   ?</span>
                        <br/>
                        <ReturnToRecommendStart>
                            <div className='nextButtonFrame'>
                                <Button className='nextButton' onClick={() => goToStart()}></Button>
                            </div>
                        </ReturnToRecommendStart>
                    </>:
                    // <>
                    //     {
                    //         thisResult.map(thisData => (
                    //             <>
                    //                 {
                    //                     (thisData.campus !== null)?
                    <>
                        <OverlayTrigger
                            key='dev'
                            placement='top'
                            overlay={
                            <Tooltip id="dev">
                                    <span>   </span>
                            </Tooltip>
                            }
                            >
                            <div className='nextButtonFrame'>
                                <Button className='nextButton' onClick={()=> setModalShow(true)}></Button>
                            </div>
                        </OverlayTrigger>

                        <br/>
                        <span>    <br/>     !!</span>
                    </>
                                        
                    //                     :
                    //                     <></>
                    //                 }
                    //             </>
                    //         ))
                    //     }
                    // </>
                }
                </>
            }
            

        </div>
        <SatisfactionModal show={modalShow} onHide={() => setModalShow(false)} />
    </BodyBlock>
  )
}

//CSS
const BodyBlock = styled.div`
    .container{
        display: grid;
        grid-template-rows: 1fr 5fr 1fr;
        background-color: white;
        text-align: center;
        /*justify-content: center;*/
        
        
        /*vertical-align: middle;*/
        row-gap: 10px;

        height: 70vh;
        width: 45vh;
    }
    

    /**/
    .notice{
        grid-row-start: 1;
        grid-row-end: 2;

        /*grid-template-rows: repeat(auto-fit, minmax(300px, auto));*/

        align-items: center;
        
        /**/
        padding-top: 3%;
        font-size: 15px;
        color: #5a5a5a;

    }

    /**/
    .resultFrame{
        grid-row-start: 2;
        grid-row-end: 3;
        /*grid-template-rows: repeat(auto-fit, minmax(300px, auto));*/

        //  
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;

        .eachResult:nth-child(odd){

            background-color: #002F5A;
            opacity: 0.8;
        
            /**/
            border-radius: 5px;
            height: 3em;
            width: 50%;
        
            /**/
            font-size: 14px;
            color: white;
            font-weight: bold;
            padding-top: 10px;
    
            /**/
            &:hover {
                background-color: #002F5A;
                opacity: 1;
            }
        }

        .eachResult:nth-child(even){

            background-color: #028799;
            opacity: 0.8;
        
            /**/
            border-radius: 5px;
            height: 3em;
            width: 50%;
        
            /**/
            font-size: 14px;
            color: white;
            font-weight: bold;
            padding-top: 10px;
    
            /**/
            &:hover {
                background-color: #028799;
                opacity: 1;
            }
        }
    }

   
    /* */
    .nextButtonFrame{
        grid-row-start: 3;
        grid-row-end: 4;
        /*grid-template-rows: repeat(auto-fit, minmax(300px, auto));*/

        //  
        display: flex;
        justify-content: center;
        align-items: center;

        .nextButton{

            background-color: #002F5A;
            opacity: 0.8;
        
            /**/
            border-radius: 5px;
            width: 60%;
        
            /**/
            font-size: 14px;
            color: white;
            font-weight: bold;
    
            /**/
            &:hover {
                background-color: #002F5A;
                opacity: 0.9;
            }
        }
    }
`

const StarFrame =styled.div`
    .star {
        position: relative;
        font-size: 2rem;
        color: #ddd;

        input {
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            opacity: 0;
            cursor: pointer;
        }

        span {
            width: 0;
            position: absolute; 
            left: 0;
            color: #002F5A;
            overflow: hidden;
            pointer-events: none;
      }
    }
`


//   CSS
const PersonalButton = styled.div`
  text-align: center;

  .recommend{
    /**/
    background-color: #002F5A;
    opacity: 0.8;

    font-size: 14px;
    width: 90%;
    height: 40%;
    margin-bottom: 8px;

    /**/
    &:hover {
        background-color: #002F5A;
        opacity: 0.9;
      }
  }

  .compete{
    /**/
    background-color: #028799;
    opacity: 0.9;

    font-size: 14px;
    width: 90%;
    height: 40%;
    margin-bottom: 8px;

    /**/
    &:hover {
        background-color: #028799;
        opacity: 1;
      }
  }

  .myPost{
    /**/
    background-color: #875100;
    opacity: 0.8;

    font-size: 14px;
    width: 90%;
    height: 40%;
    margin-bottom: 5px;

    /**/
    &:hover {
        background-color: #875100;
        opacity: 0.9;
      }
  }
`

const ReturnToRecommendStart = styled.div`
/* */
.nextButtonFrame{
  flex-grow: 0;

  //  
  // display: flex;
  justify-content: center;
  align-items: center;

  .nextButton{

      background-color: #002F5A;
      opacity: 0.8;
  
      /**/
      border-radius: 5px;
      width: 40%;
  
      /**/
      font-size: 14px;
      color: white;
      font-weight: bold;

      /**/
      &:hover {
          background-color: #002F5A;
          opacity: 0.9;
      }
  }
}
`

const FinalResult = styled.div`
    .resultFrame{
        display:flex;
    }
`