// () component
import React, { useEffect } from 'react'
import styled from 'styled-components'
import ReactTypingEffect from 'react-typing-effect';
import { Button, Col, Container, Row } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

export default function 
MainFrame() {

    //   callback 
    let navigate = useNavigate();

    const moveToSeoul = () => {
        //SeoulMain 
        navigate("/seoul");
        window.location.reload();
    }
 
    const moveToGlobal = () => {
        //GlobalMain 
        navigate("/global");
        window.location.reload();
    }

  return (
    <>
    <BodyBlock>
        <div className='container'>
            <div className='choose'>
                <ChooseBlock>
                    <div className='containerFlex'>
                        <div className='recommend' onClick={moveToSeoul}><img src={require('../../media/structure/.jpg')} alt=""/><br/><br/><span></span></div>
                        <div className='compete' onClick={moveToGlobal}><img src={require('../../media/structure/.jpg')} alt=""/><br/><br/><span></span></div>
                    </div>
                </ChooseBlock>
            </div>
            <div className='write'>  .</div>
            <div className='notice'>

                <br/><br/>
                <span>
                       ""<br/>   .<br/>
                      <b>6</b>  <br/>  .
                </span>

     
            </div>
        </div>
    </BodyBlock>
</>
)
}
//CSS
const BodyBlock = styled.div`
div.container{
 display: grid;
 grid-template-rows: 3fr 2fr 5fr;
 background-color: white;
 text-align: center;
 /*justify-content: center;*/
 
 vertical-align: middle;
 row-gap: 10px;

 height: 70vh;
 width: 45vh;
}

/* */


/*  &  */
div.choose{
 grid-row-start: 0;
 grid-row-start: 1;

 grid-template-rows: repeat(auto-fit, minmax(300px, auto));
}

/* */
div.write{
 grid-row-start: 1;
 grid-row-start: 2;
 grid-template-rows: repeat(auto-fit, minmax(300px, auto));

 

 /**/
 padding-top: 10%;

 font-size: 20px;
 color: #5a5a5a;
 font-weight: bold;

}

/**/
div.notice{
 grid-row-start: 2;
 grid-row-start: 3;

 /**/
 background-color: white;
 border: solid 1px #5a5a5a;

 /**/
 border-radius: 10px;

 /**/
 font-size: 15px;
 color: #5a5a5a;
 font-weight: normal;
}
`


const ChooseBlock = styled.div`
div.containerFlex{
 min-height: 24vh;
 display: flex;
 flex-direction: row;
 flex-wrap: nowrap;
 align-items: stretch;
 background-color: white;

}

div.recommend{
 flex-grow: 1;
 align-self: stretch;

 /**/
 background-color: #002F5A;
 opacity: 0.8;

 /**/
 margin-right: 5px;
 border-radius: 10px;

 /**/
 padding-top: 10%;
 padding-bottom: 2%;
 font-size: 19.5px;
 color: white;
 font-weight: bold;

 /**/
 &:hover {
     background-color: #002F5A;
     opacity: 0.9;
   }

 /**/
 img{
     width: 110px;
     height: 110px;
     border-radius: 10px;
     opacity: 0.8;
 }
}

div.compete{
 flex-grow: 1;
 align-self: stretch;
 
 /**/
 background-color: #028799;
 opacity: 0.9;

 /**/
 margin-left: 5px;
 border-radius: 10px;

 /**/
 padding-top: 10%;
 padding-bottom: 2%;
 font-size: 19.5px;
 color: white;
 font-weight: bold;

 /**/
 &:hover {
     background-color: #028799;
     opacity: 1;
   }
 
 /**/
 img{
     width: 110px;
     height: 110px;
     border-radius: 10px;
     opacity: 0.8;
 }
}
`