// () component
import React, { useEffect } from 'react'
import styled from 'styled-components'
// import ReactTypingEffect from 'react-typing-effect';
import { Button } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2'   

export default function MainFrame() {

    //   callback 
    let navigate = useNavigate();

    //    (1 )
    useEffect( () =>{
        //recommendTest :         (url    )
        sessionStorage.setItem('recommendTest', 'true');

        //questionNum :       
        sessionStorage.setItem('questionNum', 0);
    },[])



    const moveToQuestion = () => {

        //question page 
        navigate("/question1");
        // window.location.reload();
    }

    return (
    <>
        <BodyBlock>
            <div className='container'>
                <div className='ShowFrame'>
                    <img src={require('../../../media/major/aniMajor.gif')} alt=' '/>
                </div>
                <div className='startFrame'>
                    <Button className='startButton' onClick={moveToQuestion}></Button>
                </div>
                <div className='notice'>
                    <span> .</span><br/>
                    <span>  '' !</span><br/>
                    
                </div>
            </div>
        </BodyBlock>
    </>
  )
}

//CSS
const BodyBlock = styled.div`
    .container{
        display: grid;
        grid-template-rows: 3fr 1fr 1fr 5fr;
        background-color: white;
        text-align: center;
        /*justify-content: center;*/
        
        
        vertical-align: middle;
        row-gap: 10px;

        height: 70vh;
        width: 45vh;
    }
    

    /* gif*/
    .ShowFrame{
        gird-row-start: 0;
        grid-row-start: 1;

        grid-template-rows: repeat(auto-fit, minmax(300px, auto));

        display: flex;
        justify-content: center;
        align-items: center;

        /**/
        border-radius: 10px;
        
        /**/
        background-color: #002F5A;
        opacity: 0.8;


        img{
            width: 70%;
            border-radius: 10px;
            // vertical-align: middle;        
        }
    }

    /*gif */
    .gifFrame{
        width: 150px;
    }

 /**/
    .startFrame{
        gird-row-start: 2;
        grid-row-start: 3;
        grid-template-rows: repeat(auto-fit, minmax(300px, auto));

        //  
        display: flex;
        justify-content: center;
        align-items: center;

        .startButton{

            background-color: #002F5A;
            opacity: 0.8;
        
            /**/
            border-radius: 5px;
            width: 40%;
        
            /**/
            font-size: 14px;
            color: white;
            font-weight: bold;
    
            /**/
            &:hover {
                background-color: #002F5A;
                opacity: 0.9;
            }
        }

    }
    


 /**/
 .notice{
     gird-row-start: 3;
     grid-row-start: 4;    

     /**/
     padding-top: 3%;
     font-size: 15px;
     color: #5a5a5a;
     opacity: 1;
 }
`


const ChooseBlock = styled.div`
 div.containerFlex{
     min-height: 24vh;
     display: flex;
     flex-direction: row;
     flex-wrap: nowrap;
     align-items: stretch;
     background-color: white;

 }

 div.recommend{
     flex-grow: 1;
     align-self: stretch;

     /**/
     background-color: #002F5A;
     opacity: 0.8;

     /**/
     margin-right: 5px;
     border-radius: 10px;

     /**/
     padding-top: 10%;
     font-size: 19.5px;
     color: white;
     font-weight: bold;

     /**/
     &:hover {
         background-color: #002F5A;
         opacity: 0.9;
       }

     /**/
     img{
         width: 45px;
         height: 45px;
     }
 }

 div.compete{
     flex-grow: 1;
     align-self: stretch;
     
     /**/
     background-color: #028799;
     opacity: 0.9;

     /**/
     margin-left: 5px;
     border-radius: 10px;

     /**/
     padding-top: 10%;
     font-size: 19.5px;
     color: white;
     font-weight: bold;

     /**/
     &:hover {
         background-color: #028799;
         opacity: 1;
       }
     
     /**/
     img{
         width: 45px;
         height: 45px;
     }
 }
`



